C;AAEA;IACI,aAAa;AACjB;AAEA;IACI,YAAY;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from \"vue\";\nimport { ITheme } from \"@stechquick/algae/lib/quick/IPlateauUI\";\ninterface IThemeCardProp {\n    theme: ITheme,\n    selectedThemeName: string,\n    showDeleteIcon: boolean,\n    showRadioIcon: boolean\n}\ntype EmitNames =\n    \"cardClick\" |\n    \"pencilClick\" |\n    \"deleteClick\" |\n    \"radioClick\"\nconst props = defineProps<IThemeCardProp>();\nconst emit = defineEmits(['cardClick', 'pencilClick', 'deleteClick', 'radioClick']);\nconst selectedThemeName = computed(() => props.selectedThemeName)\n\nfunction emitFunc(functionName: EmitNames, ...args: any) {\n    emit(functionName, args);\n}\n</script>\n\n<template>\n    <div class=\"theme-card\">\n        <div class=\"actions\" @click=\"emitFunc('cardClick')\">\n            <div class=\"theme-title\">\n                <span class=\"title\">{{ theme.name }}</span>\n                <span class=\"description\">Roboto Font Family, +2</span>\n            </div>\n            <div class=\"action-buttons\">\n                <i class=\"mdi mdi-pencil-outline\" @click=\"emitFunc('pencilClick', theme.name)\"></i>\n                <i v-if=\"showDeleteIcon\" @click=\"emitFunc('deleteClick')\" class=\"mdi mdi-delete-outline\"></i>\n                <i v-if=\"showRadioIcon && selectedThemeName == theme.name\" class=\"mdi mdi-radiobox-marked\"></i>\n                <i v-else-if=\"showRadioIcon && selectedThemeName != theme.name\" class=\"mdi mdi-radiobox-blank\"\n                    @click=\"emitFunc('radioClick', theme.name)\"></i>\n            </div>\n        </div>\n        <div class=\"theme-colors\">\n            <div v-for=\"color in theme.colors.slice(0, 5)\" :key=\"color.color\" :style=\"{ backgroundColor: color.color }\"\n                class=\"buttom-color col\"></div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n.actions {\n    display: flex;\n    justify-content: space-between;\n    cursor: pointer;\n    padding: 20px 24px;\n}\n\n.themes-section .action-buttons i {\n    font-size: 20px;\n    color: #757575;\n}\n\n.action-buttons {\n    display: flex;\n    justify-content: space-around;\n    gap: 12px;\n}\n\n\n.theme-card {\n    height: 115px;\n    border-radius: 10px;\n    border: 1px #E3E3E3 solid;\n    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);\n}\n\n.theme-card .title {\n    font-family: Roboto;\n    font-size: 20px;\n    font-weight: 500;\n    line-height: 23px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #212121;\n}\n\n.theme-card .description {\n    font-family: Roboto;\n    font-size: 16px;\n    font-weight: 400;\n    line-height: 20px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #757575;\n    margin-top: 8px;\n}\n\n.theme-title {\n    display: grid;\n}\n\n.buttom-color:last-of-type {\n    border-bottom-right-radius: 10px;\n}\n\n.buttom-color:nth-of-type(1) {\n    border-bottom-left-radius: 10px;\n}\n\n.theme-colors {\n    display: flex;\n}\n\n.buttom-color {\n    height: 24px;\n}\n</style>"],"sourceRoot":""}]);
