import * as monaco from "monaco-editor";
import loader from "@monaco-editor/loader";

export interface IEditorContainer { [key: string]: monaco.editor.IStandaloneCodeEditor };

export class IntegrationsEditorManager {
    public static initMonacoEditor({ source, code, editorContainer, editorLanguage }: { source: string, code: string, editorContainer: IEditorContainer, editorLanguage: "html" | "typescript" }) {
        loader.init().then((monaco) => {
            const container = document.getElementById(`integration-editor-${source}`) as HTMLElement;
            container.classList.add("editor-style");
            const editor = monaco.editor.create(container, {
                value: code,
                language: editorLanguage,
                theme: "vs",
                automaticLayout: true,
                minimap: {
                    enabled: false
                }
            });
            editorContainer[source] = editor;
            const model = editor.getModel();
            if (!model) return;

            // const languages = ["html"];
            // const languageSelect = document.createElement("select");
            // languages.forEach((lang) => {
            //     const option = document.createElement("option");
            //     option.text = lang;
            //     option.value = lang;
            //     languageSelect.appendChild(option);
            // });

            // languageSelect.addEventListener("change", () => {
            //     monaco.editor.setModelLanguage(model, languageSelect.value);
            // });

            // container.appendChild(languageSelect);

            // languageSelect.style.position = 'absolute';
            // languageSelect.style.top = '-32px';
            // languageSelect.style.right = '16px';
        });
    }
}