us: 4px;\n    opacity: 1;\n    margin-top: 3px;\n    padding-left: 7px;\n    font-family: \"ROBOTO\";\n    font-size: 14px;\n    color: #151233;\n}\n.custom-input[data-v-64e2ed43]::placeholder {\n    color: #A1A1A1;\n}\n\n/* .custom-input:focus {\n    border-color: #29C660;\n} */\n.custom-input[data-v-64e2ed43]:disabled {\n    opacity: 0.7;\n    cursor: not-allowed;\n    background: #E3E3E3;\n}\n.custom-input[data-v-64e2ed43]:disabled::placeholder {\n    color: #BCBCBC;\n}\n.table-container[data-v-64e2ed43] {\n    max-height: 400px;\n    overflow-y: auto;\n}\ntable[data-v-64e2ed43] {\n    border-collapse: separate !important;\n    border-spacing: 0;\n}\n.custom-table[data-v-64e2ed43] {\n  width: 100%;\n  min-height: 85px;\n  border-collapse: collapse;\n  border: 1px solid #e3e3e3;\n  border-radius: 10px;\n}\n.header-cell[data-v-64e2ed43] {\n  background-color: #f5f5f5;\n  font-weight: 500;\n  padding: 10px;\n  border-top-right-radius: 10px;\n  border-top-left-radius: 10px;\n}\ntd[data-v-64e2ed43] {\n  border-top: 1px solid #e3e3e3;\n  padding: 10px;\n  display: flex;\n  justify-content: space-between;\n}\n.row-icon-container[data-v-64e2ed43] {\n  display: flex;\n  justify-content: flex-end;\n  align-items: center;\n}\n.row-icon-container i[data-v-64e2ed43] {\n  margin-left: 10px;\n  cursor: pointer;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/IntegrationsSettings/customIntegrationPage.vue"],"names":[],"mappings":";AAsQA;IACI,aAAa;IACb,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,gBAAgB;AACpB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;AAEA;IACI,iBAAiB;AACrB;AAEA;IACI,gBAAgB;IAChB,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,kBAAkB;AACtB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,gCAAgC;AACpC;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,SAAS;IACT,iBAAiB;AACrB;AAEA;IACI,cAAc;IACd,gCAAgC;IAChC,iBAAiB;AACrB;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,aAAa;AACjB;AAEA;;;IAGI,mBAAmB;AACvB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;AAEA;;IAEI,cAAc;IACd,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,qBAAqB;IACrB,mBAAmB;IACnB,UAAU;AACd;AAEA;IACI,mBAAmB;AACvB;AAEA;IACI,kBAAkB;AACtB;AAEA;IACI,aAAa;IACb,cAAc;IACd,eAAe;IACf,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,YAAY;AAChB;AAEA;IACI,eAAe;IACf,cAAc;IACd,kBAAkB;AACtB;AAEA;IACI;AACJ;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,yBAAyB;AAC7B;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;AAEA;;IAEI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;IACf,gBAAgB;IAChB,qBAAqB;IACrB,eAAe;AACnB;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;AAEA;IACI,yBAAyB;IACzB,cAAc;AAClB;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;AAEA;IACI,WAAW;IACX,YAAY;IACZ,yBAAyB;IACzB,cAAc;IACd,yBAAyB;IACzB,eAAe;IACf,UAAU;IACV,gBAAgB;IAChB,qBAAqB;IACrB,eAAe;IACf,kBAAkB;AACtB;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,UAAU;IACV,eAAe;IACf,iBAAiB;IACjB,qBAAqB;IACrB,eAAe;IACf,cAAc;AAClB;AAEA;IACI,cAAc;AAClB;;AAEA;;GAEG;AAEH;IACI,YAAY;IACZ,mBAAmB;IACnB,mBAAmB;AACvB;AAEA;IACI,cAAc;AAClB;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;AAEA;IACI,oCAAoC;IACpC,iBAAiB;AACrB;AACA;EACE,WAAW;EACX,gBAAgB;EAChB,yBAAyB;EACzB,yBAAyB;EACzB,mBAAmB;AACrB;AAEA;EACE,yBAAyB;EACzB,gBAAgB;EAChB,aAAa;EACb,6BAA6B;EAC7B,4BAA4B;AAC9B;AAEA;EACE,6BAA6B;EAC7B,aAAa;EACb,aAAa;EACb,8BAA8B;AAChC;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,mBAAmB;AACrB;AAEA;EACE,iBAAiB;EACjB,eAAe;AACjB","sourcesContent":["<template>\n    <div class=\"page-container\" v-show=\"!showAddNewEventPage\">\n        <div class=\"page-header\">\n            <img class=\"custom-integration-icon\" src=\"./customIntegrationIcon.svg\" alt=\"Custom Integration Icon\" />\n            <div class=\"header-text-container\">\n                <div class=\"header-title\">Custom Integration</div>\n                <div class=\"header-description\">This is the main page for custom integration. Here you can create your own\n                    custom integration.</div>\n            </div>\n        </div>\n        <div class=\"page-content\">\n            <div class=\"tabs-container\">\n                <div :class=\"'tab' + (showingTab === 'General' ? ' selected' : '')\" @click=\"selectedTab('General')\"\n                    style=\"width: 157px;\">\n                    <span>General</span>\n                </div>\n                <div :class=\"'tab' + (showingTab === '<head> Code' ? ' selected' : '')\" @click=\"selectedTab('<head> Code')\"\n                    style=\"width: 190px;\">\n                    <span>{{ '<head> Code' }}\n                    </span>\n                </div>\n                <div :class=\"'tab' + (showingTab === '<body> Code' ? ' selected' : '')\" @click=\"selectedTab('<body> Code')\"\n                    style=\"width: 189px;\">\n                    <span>{{ '<body> Code' }}\n                    </span>\n                </div>\n                <div :class=\"'tab' + (showingTab === '<footer> Code' ? ' selected' : '')\"\n                    @click=\"selectedTab('<footer> Code')\" style=\"width: 197px;\">\n                    <span>{{ '<footer> Code' }}</span>\n                </div>\n                <div :class=\"'tab' + (showingTab === 'Events' ? ' selected' : '')\" @click=\"selectedTab('Events')\"\n                    style=\"width: 150px;\">\n                    <span>Events</span>\n                </div>\n            </div>\n            <div class=\"tab-content\">\n                <div v-show=\"showingTab === 'General'\">\n                    <div class=\"input-name\">\n                        <IDEInput :isRequired=\"true\" elementId=\"name-input\" @getValue=\"getCustomIntegrationName\" placeholder=\"Custom Integration Name\" type=\"text\" info-text=\"Integration Name\" :value=\"customIntegrationName\" />\n                    </div>\n                    <div class=\"input-description\">\n                        <IDEInput elementId=\"description-input\" @getValue=\"getCustomIntegrationDesc\" placeholder=\"Custom Integration Description\" type=\"textarea\" info-text=\"Description\" :value=\"customIntegrationDescription\" />\n                    </div>\n                    <div v-show=false class=\"input-load-options\">\n                        <div class=\"options-title\">\n                            <span>Load Options</span>\n                        </div>\n                        <div class=\"radio-groups-container\">\n                            <div class=\"radio-group\">\n                                <RadioButton type=\"export\" @click=\"toggleRadio\" :active=\"loadOnceRadio\" />\n                                <span class=\"radio-text\">Load once</span>\n                            </div>\n                            <div class=\"radio-group\">\n                                <RadioButton type=\"export\"  @click=\"toggleRadio\" :active=\"loadEverytimeRadio\" />\n                                <span class=\"radio-text\">Load everytime</span>\n                            </div>\n                        </div>\n                    </div>\n                    <div class=\"doc-info\">\n                        <i class=\"mdi mdi-information\"></i>\n                        <span>Use <a class=\"doc-link\"\n                                href=\"https://docs.onplateau.com/UI-Screens/UI-Settings/custom-integration\"\n                                target=\"_blank\">Custom Integration Documentation</a> for detailed information.</span>\n                    </div>\n\n                </div>\n                <div v-show=\"showingTab === '<head> Code'\">\n                    <div class=\"code-tab-title\">\n                        <span>{{ \"Add Custom <head> Code\" }}\n                        </span>\n                    </div>\n                    <div id=\"integration-editor-head\"></div>\n                </div>\n                <div v-show=\"showingTab === '<body> Code'\">\n                    <div class=\"code-tab-title\">\n                        <span>{{ \"Add Custom <body> Code\" }}\n                        </span>\n                    </div>\n                    <div id=\"integration-editor-body\"></div>\n                </div>\n                <div v-show=\"showingTab === '<footer> Code'\">\n                    <div class=\"code-tab-title\">\n                        <span>{{ \"Add Custom <footer> Code\" }} </span>\n                    </div>\n                    <div id=\"integration-editor-footer\"></div>\n                </div>\n                <div v-show=\"showingTab === 'Events'\">\n                    <div class=\"code-tab-title\">\n                        <div class=\"event-content-head\">\n                            <input id=\"search-input\" class=\"custom-input\" type=\"text\" v-model=\"searchQuery\" placeholder=\"Search events\">                            \n                            <button class=\"add-new-button\" @click=\"openEvent()\">ADD NEW</button>\n                        </div>\n                        <div class=\"table-container\">\n                            <table class=\"custom-table\">\n                                <thead>\n                                <tr>\n                                    <th class=\"header-cell\">Event Name</th>\n                                </tr>\n                                </thead>\n                                <tbody>\n                                    <tr v-for=\"(event, index) in filteredEvents\" :key=\"index\" v-if=\"filteredEvents.length > 0\">\n                                        <td>\n                                            <span>{{ event.eventName }}</span>\n                                            <span class=\"row-icon-container\">\n                                                <i class=\"mdi mdi-pencil-outline\" @click=\"editEvent(index)\"></i>\n                                                <i class=\"mdi mdi-delete-outline\" @click=\"deleteEvent(index)\"></i>\n                                            </span>\n                                        </td>\n                                    </tr>\n                                    <tr v-if=\"filteredEvents.length === 0\">\n                                        <td>No event data</td>\n                                    </tr>\n                                </tbody>\n                            </table>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div class=\"page-footer\">\n            <div class=\"footer-buttons\">\n                <button class=\"btn btn-cancel\" @click=\"cancel\">Cancel</button>\n                <button class=\"btn btn-save\" @click=\"save\">SAVE</button>\n            </div>\n        </div>\n    </div>\n    <div v-if=\"showAddNewEventPage\">\n        <AddNewEventPage :eventData=\"currentEvent!\" :index=\"currentEvent!.index\" @savedEventData=\"handleSavedEvent\" @cancel=\"showAddNewEventPage = false\" :eventList=\"events\"></AddNewEventPage>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, computed, onMounted } from 'vue';\nimport IDEInput from \"../IDEInput.vue\";\nimport RadioButton from '../../radioButtonComp.vue';\nimport { ICustomIntegrationItem, IEventData } from \"@stechquick/algae/lib/quick/IntegrationSettings\"\nimport AddNewEventPage from \"./addNewEventPage.vue\";\nimport { IEditorContainer, IntegrationsEditorManager } from \"./common/IntegrationsEditorManager\"\nimport { INotification } from \"../../../../../domain/presentation/INotification\";\nimport { container } from '../../../../../domain/core/diContainer';\n\nconst Notification = container.resolve<INotification>(INotification);\n\nconst emit = defineEmits([\"cancel\", \"savedItemData\"]);\nconst props = defineProps<{\n    data: ICustomIntegrationItem;\n    index?: number;\n    customIntegrationList: Array<ICustomIntegrationItem>;\n}>();\nlet showAddNewEventPage = ref(false);\nconst customIntegrationDescription = ref(props.data.description);\nconst customIntegrationName = ref(props.data.name);\nlet loadOnceRadio = ref(props.data.loadOption.loadOnce);\nlet loadEverytimeRadio = ref(props.data.loadOption.loadEverytime);\nconst events = ref(props.data.events ?? []);\nlet currentEvent = ref<IEventData>();\nconst customIntegrationList = ref(props.customIntegrationList ?? []);\n\nconst editors: IEditorContainer = {};\nconst showingTab = ref(<\"General\" | \"<head> Code\" | \"<body> Code\" | \"<footer> Code\" | \"Events\">(\"General\"));\nconst searchQuery = ref('');\n\nfunction toggleRadio() {\n    loadOnceRadio.value = !loadOnceRadio.value;\n    loadEverytimeRadio.value = !loadEverytimeRadio.value;\n}\nfunction editEvent(index: number) {\n    const eventData = events.value[index];\n    openEvent(eventData, index);\n};\n\nfunction openEvent(eventData?: IEventData, index?: number) {\n    if (eventData) {\n        currentEvent.value = Object.assign({}, eventData, { index });\n    } else {\n        currentEvent.value = {\n            eventName: \"\",\n            returnValue: { hasReturnValue: false },\n            eventContent: \"\"\n        };\n    }\n    showAddNewEventPage.value = true;\n};\n\n\nfunction deleteEvent(index: number) {\n    events.value.splice(index, 1);\n};\n\nfunction selectedTab(val: \"General\" | \"<head> Code\" | \"<body> Code\" | \"<footer> Code\" | \"Events\") {\n    showingTab.value = val;\n    if (val === 'General' || val === 'Events') return;\n}\n\nonMounted(() => {\n    IntegrationsEditorManager.initMonacoEditor({ source: 'head', code: props.data.headCode ?? \"\", editorContainer: editors, editorLanguage: \"html\" });\n    IntegrationsEditorManager.initMonacoEditor({ source: 'body', code: props.data.bodyCode ?? \"\", editorContainer: editors, editorLanguage: \"html\" });\n    IntegrationsEditorManager.initMonacoEditor({ source: 'footer', code: props.data.footerCode ?? \"\", editorContainer: editors, editorLanguage: \"html\" });\n});\n\nfunction cancel() {\n    emit(\"cancel\");\n}\n\nfunction save() {\n    if (!customIntegrationName.value) {\n        Notification.showNotification({ text: \"Integration Name is required\", type: \"error\" });\n        return;\n    }\n    for (const item of customIntegrationList.value) {\n        const currentName = customIntegrationName.value.trimStart().trimEnd();\n        if (item.name === currentName && (props.index === undefined || props.index !== item.index)) {\n            Notification.showNotification({ text: `Integration Name ${currentName} already exists. Please consider another name.`, type: \"error\" });\n            return;\n        }\n    }\n    if (editors['head'].getValue() === \"\" && editors['body'].getValue() === \"\" && editors['footer'].getValue() === \"\") {\n        Notification.showNotification({ text: \"At least one of the code sections should be filled\", type: \"error\" });\n        return;\n    }\n\n    const integrationItem: ICustomIntegrationItem = {\n        name: customIntegrationName.value,\n        description: customIntegrationDescription.value,\n        loadOption: {\n            loadOnce: loadOnceRadio.value,\n            loadEverytime: loadEverytimeRadio.value\n        },\n        headCode: editors['head']?.getValue() ?? '',\n        bodyCode: editors['body']?.getValue() ?? '',\n        footerCode: editors['footer']?.getValue() ?? '',\n        events: events.value,\n        index: props.index\n    };\n    emit(\"savedItemData\", integrationItem);\n}\n\nconst filteredEvents = computed(() => {\n    return events.value.filter(event => event.eventName.toLowerCase().includes(searchQuery.value.toLowerCase()));\n});\n\nfunction getCustomIntegrationName(value: string) {\n    customIntegrationName.value = value;\n};\n\nfunction getCustomIntegrationDesc(value: string) {\n    customIntegrationDescription.value = value;\n};\n\nfunction handleSavedEvent(savedEventData: IEventData) {\n    if (savedEventData.index !== undefined) { // !== undefined because index 0 results in false \n        events.value.splice(savedEventData.index, 1, savedEventData);\n    } else {\n        savedEventData.index = events.value.length;\n        events.value.push(savedEventData);\n    }\n    showAddNewEventPage.value = false;\n};\n\n</script>\n\n<style scoped>\n.editor-style {\n    height: 430px;\n    width: 100%;\n    border: 1px solid #BCBCBC;\n    position: relative;\n    padding-top: 8px;\n}\n\n.page-container {\n    margin-top: 40px;\n}\n\n.page-header {\n    display: flex;\n    align-items: center;\n}\n\n.header-text-container {\n    margin-left: 24px;\n}\n\n.header-title {\n    font-weight: 500;\n    font-size: 20px;\n    line-height: 23px;\n    color: #151233;\n    margin-bottom: 8px;\n}\n\n.header-description {\n    font-size: 16px;\n    line-height: 19px;\n    text-align: left;\n    color: #616161;\n    font-weight: 400;\n}\n\n.page-content {\n    margin-top: 32px;\n}\n\n.tabs-container {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    width: 100%;\n    height: 36px;\n    border-bottom: 1px solid #BDBDBD;\n}\n\n.tab {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100%;\n    font-size: 14px;\n    line-height: 17px;\n    color: #A1A1A1;\n    font-weight: 500;\n    cursor: pointer;\n    padding: 10px 54px;\n    gap: 10px;\n    user-select: none;\n}\n\n.tab.selected {\n    color: #151233;\n    border-bottom: 2px solid #151233;\n    user-select: none;\n}\n\n.tab-content {\n    margin-top: 24px;\n    width: 100%;\n    height: 450px;\n}\n\n.input-name,\n.input-description,\n.input-load-options {\n    margin-bottom: 24px;\n}\n\n.radio-groups-container {\n    display: flex;\n    align-items: center;\n    gap: 24px;\n}\n\n.radio-text {\n    font-size: 13px;\n    line-height: 16p