  dialog.showDialog(
    defineAsyncComponent(() => import(`../components/dialogs/appLogoDialog.vue`)),
    { closable: true, title: "Select App Image", width: "530px" },
    { app: appParam }
  );
}

function onWebLottieSelect(lottieName: string) {
  // settingsModel.value!.general.webLoading = lottieName;
  const modelID = getModelIDInModelMapByName(lottieName);
  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);
  settingsModel.value!.general.webLoading = val;
  newSettingsModel.general.webLoading = val;
  inputChanged();
}

function onMobileLottieSelect(lottieName: string) {
  const modelID = getModelIDInModelMapByName(lottieName);
  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);
  settingsModel.value!.general.mobileLoading = val;
  newSettingsModel.general.mobileLoading = val;
  inputChanged();
}

function onInputSiteTitle($event: any) {
  settingsModel.value!.general.siteTitle = $event.target.value;
  newSettingsModel.general.siteTitle = $event.target.value;
  inputChanged();
}
function onOAuthSettingsChange(params: { [key: string]: string }) {
  const changedKey = Object.keys(params)[0];

  (<any>oAuthSettingsModel.value)[changedKey] = params[changedKey];
  settingsModel.value!.oAuthSettings = oAuthSettingsModel.value;
  newSettingsModel.oAuthSettings = oAuthSettingsModel.value;
  if (newSettingsModel.oAuthSettings.unsecurePages?.[0] == "") {
    delete newSettingsModel.oAuthSettings.unsecurePages;
  }
  inputChanged();
}

function importDialog() {
  dialog.showDialog<{ param: INewItemPropsInline }>(
    asyncComponentCreator.createInputModelDialog(),
    { closable: true, title: "Upload Local File", closeOnOutClick: true, maxHeight: "487px", width: "600px" },
    { param: { ownerId: viewModel.studio.appId, path: "/" } },
  );
}

function addEventListeners() {
  document.getElementById("add-web-lottie")!.addEventListener("click", importDialog);
  document.getElementById("add-mobile-lottie")!.addEventListener("click", importDialog);
}

function selectTab(val: "General" | "Themes" | "oAuth" | "Integrations" | "App Versions") {
  showingTab.value = val;
  if (val != "Themes" && val != "App Versions") {
    showHeader();
  }
}

function getModelNameInModelMapByID(ID?: string) {
  return modelMap.value.find(i => i.ID == ID)?.name;
}

function getModelIDInModelMapByName(name?: string) {
  return modelMap.value.find(i => i.name == name)?.ID;
}

