  font-size: 14px;\n    color: #151233;\n}\n.custom-input-value[data-v-4e38dd16] {\n    margin-right: 30px;\n}\n.custom-input[data-v-4e38dd16]::placeholder {\n    color: #A1A1A1;\n}\n.no-param[data-v-4e38dd16] {\n    color: #151233;\n    font-style: normal;\n    font-weight: bold;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n}\n\n/* .custom-input:focus {\n    border-color: #29C660;\n} */\n.custom-input[data-v-4e38dd16]:disabled {\n    opacity: 0.7;\n    cursor: not-allowed;\n    background: #E3E3E3;\n}\n.custom-input[data-v-4e38dd16]:disabled::placeholder {\n    color: #BCBCBC;\n}\n.page-footer[data-v-4e38dd16] {\n    width: 100%;\n    height: 88px;\n    display: flex;\n    align-items: center;\n    justify-content: flex-end;\n}\n.footer-buttons[data-v-4e38dd16] {\n    display: flex;\n    align-items: center;\n    gap: 8px;\n}\n.return-switch[data-v-4e38dd16] {\n    position: relative;\n    cursor: pointer;\n    top: 0;\n    bottom: 0;\n    background-color: #ccc;\n    -webkit-transition: 0.4s;\n    transition: 0.4s;\n    margin-right: 13px;\n    margin-left: auto;\n}\n.return-switch[data-v-4e38dd16]:after {\n    position: absolute;\n    content: \"\";\n    height: 14px;\n    width: 14px;\n    background-color: white;\n    -webkit-transition: 0.4s;\n    transition: 0.4s;\n    border-radius: 50%;\n}\n.return-switch[data-v-4e38dd16]:disabled:after {\n    background-color: #757575;\n}\n.return-switch[data-v-4e38dd16]:checked {\n    background-color: #151233;\n}\ninput[type=checkbox][data-v-4e38dd16] {\n    -webkit-appearance: none;\n    -moz-appearance: none;\n    appearance: none;\n    -webkit-tap-highlight-color: transparent;\n    cursor: pointer;\n}\n.return-switch[data-v-4e38dd16]:checked:after {\n    transform: translatex(16px);\n}\n.return-switch.round[data-v-4e38dd16]::after {\n    left: 3px;\n    border-radius: 50%;\n}\n.return-switch.round[data-v-4e38dd16] {\n    width: 36px;\n    height: 20px;\n    border-rad