lB,iBAAiB;IACjB,eAAe;IACf,qBAAqB;IACrB,mBAAmB;IACnB,UAAU;IACV,cAAc;AAClB;AAEA;IACI,gBAAgB;IAChB,kBAAkB;AACtB;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["<template>\n    <div class=\"page-container\">\n        <div class=\"page-header\">\n            <div class=\"header-title\">Events</div>\n        </div>\n        <div class=\"page-content\">\n            <div class=\"tabs-container\">\n                <div :class=\"'tab' + (showingTab === 'General' ? ' selected' : '')\" @click=\"selectedTab('General')\"\n                    style=\"width: 157px;\">\n                    <span>General</span>\n                </div>\n                <div :class=\"'tab' + (showingTab === 'Content' ? ' selected' : '')\" @click=\"selectedTab('Content')\"\n                    style=\"width: 190px;\">\n                    <span>{{ 'Content' }}</span>\n                </div>\n            </div>\n            <div class=\"tab-content\">\n                <div v-show=\"showingTab === 'General'\">\n                    <div class=\"input-name\">\n                        <IDEInput :isRequired=\"true\" elementId=\"event-name-input\" @getValue=\"getEventName\"\n                            placeholder=\"Event Name\" type=\"text\" info-text=\"Event Name\" :value=\"eventName\" />\n                    </div>\n                    <div class=\"return-container\">\n                        <div>\n                            <span class=\"return-text\">Is there Return Value?</span>\n                            <div class=\"switch-container\">\n                                <input class=\"return-switch round\" type=\"checkbox\" v-model=\"isActive\"\n                                    @click=\"isActive = !isActive, setActive(isActive)\" />\n                                <span style=\"position: absolute;\">{{ isActive ? \"Yes\" : \"No\" }}</span>\n                            </div>\n                        </div>\n                        <div class=\"return-inputs-container\">\n                            <div class=\"return-inputs\">\n                                <span class=\"return-text\">Return Type</span>\n                                <CustomSelect :options=\"filterOptions\" :disabled=\"!isActive\" @input=\"filterTable($event, undefined, true)\"\n                                    class=\"table-filters\" :selected-class=\"'selected-filter '\"\n                                    :selected-option=\"returnSelectedFilter\">\n                                </CustomSelect>\n                            </div>\n                            <div class=\"return-inputs\">\n                                <IDEInput :isRequired=\"true\" :is-disabled=\"disableContentArea\"\n                                    elementId=\"object-content-input\" @getValue=\"getReturnObjectContent\"\n                                    placeholder=\"Content\" type=\"text\" info-text=\"Object Content\"\n                                    :value=\"returnObjectContent\" />\n                            </div>\n\n                        </div>\n                    </div>\n                    <div class=\"event-parameter-head\">\n                        <div class=\"param-title\">\n                            <span>Parameters</span>\n                        </div>\n                        <button class=\"add-new-button\" @click=\"addNewParameter\">ADD NEW</button>\n                    </div>\n\n                    <div class=\"table-container\">\n                        <table class=\"custom-table\">\n                            <thead>\n                                <tr>\n                                    <th class=\"name-column\">Name</th>\n                                    <th class=\"type-column\">Type</th>\n                                    <th class=\"value-column\">Object Content</th>\n                                    <th class=\"icon-column\"></th>\n                                </tr>\n                            </thead>\n                            <tbody>\n                                <tr v-for=\"(parameter, index) in parameters\" :key=\"index\">\n                                    <td>\n                                        <input class=\"custom-input-name\" type=\"text\" placeholder=\"Name\" required:true\n                                            v-model=\"parameter.parameterName\">\n                                    </td>\n                                    <td>\n                                        <CustomSelect :options=\"filterOptions\" @input=\"filterTable($event, index)\"\n                                            class=\"table-filters-param\" :selected-class=\"'selected-filter '\"\n                                            :selected-option=\"parameter.parameterType\">\n                                        </CustomSelect>\n                                    </td>\n                                    <td>\n                                        <IDEInput :isRequired=\"true\" :is-disabled=\"disableParamObjectContent(index)\"\n                                            elementId=\"object-content-input\" @getValue=\"(val) => getParamObjectContent(val, index)\"\n                                            placeholder=\"Content\" type=\"text\" :value=\"parameter.parameterObjectContent\" />\n                                    </td>\n                                    <td>\n                                        <div class=\"row-icon-container\">\n                                            <i class=\"mdi mdi-delete-outline\" @click=\"deleteParameter(index)\"></i>\n                                        </div>\n                                    </td>\n                                </tr>\n                                <tr v-if=\"parameters.length === 0\">\n                                    <td class=\"no-param\">No parameter data</td>\n                                    <td></td>\n                                    <td></td>\n                                </tr>\n                            </tbody>\n                        </table>\n                    </div>\n\n                </div>\n                <div v-show=\"showingTab === 'Content'\">\n                    <div id=\"integration-editor-event-content\"></div>\n                </div>\n            </div>\n        </div>\n        <div class=\"page-footer\">\n            <div class=\"footer-buttons\">\n                <button class=\"btn btn-cancel\" @click=\"cancel\">Cancel</button>\n                <button class=\"btn btn-save\" @click=\"save\">SAVE</button>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { onMounted, ref } from 'vue';\nimport { IEventData, IntegrationSelectableTypes } from \"@stechquick/algae/lib/quick/IntegrationSettings\";\nimport IDEInput from \"../IDEInput.vue\";\nimport CustomSelect from '../../customSelect.vue';\nimport { IEditorContainer, IntegrationsEditorManager } from './common/IntegrationsEditorManager';\nimport { INotification } from \"../../../../../domain/presentation/INotification\";\nimport { container } from '../../../../../domain/core/diContainer';\n\nconst Notification = container.resolve<INotification>(INotification);\nconst emit = defineEmits([\"cancel\", \"savedEventData\"]);\nconst props = defineProps<{\n    eventData: IEventData;\n    index?: number;\n    eventList: IEventData[];\n}>();\nconst filterOptions = ref<Array<IntegrationSelectableTypes>>([\"number\", \"boolean\", \"object\", \"string\"]);\nconst isActive = ref(props.eventData.returnValue.hasReturnValue);\nconst eventName = ref(props.eventData.eventName);\nconst parameters = ref(props.eventData.parameters ?? []);\nconst returnSelectedFilter = ref(props.eventData.returnValue.returnValueType);\nconst returnObjectContent = ref(props.eventData.returnValue.objectContent);\nconst showingTab = ref(<\"General\" | \"Content\">(\"General\"));\nconst disableContentArea = ref(returnObjectContent.value ? false : true);\nconst eventList = ref(props.eventList ?? []);\n\nconst editors: IEditorContainer = {};\n\nonMounted(() => {\n    IntegrationsEditorManager.initMonacoEditor({ source: 'event-content', code: props.eventData.eventContent, editorContainer: editors, editorLanguage: \"typescript\" });\n});\n\nfunction deleteParameter(index: number) {\n    parameters.value.splice(index, 1);\n}\n\nfunction selectedTab(val: \"General\" | \"Content\") {\n    showingTab.value = val;\n    // if (val === 'General' || val === 'Events') return;\n}\n\nfunction cancel() {\n    emit(\"cancel\");\n}\n\nfunction save() {\n    if (!eventName.value) {\n        Notification.showNotification({ text: \"Event Name is required\", type: \"error\" });\n        return;\n    }\n    for (const event of eventList.value) {\n        const currentName = eventName.value.trimStart().trimEnd();\n        if (event.eventName === currentName && (props.index === undefined || props.index !== event.index)) {\n            Notification.showNotification({ text: `Event ${currentName} already exists in your current events. Please consider another name.`, type: \"error\" });\n            return;\n        }\n    }\n    const eventData: IEventData = {\n        eventName: eventName.value,\n        parameters: parameters.value,\n        eventContent: editors[\"event-content\"]?.getValue() ?? '',\n        returnValue: { hasReturnValue: isActive.value, returnValueType: returnSelectedFilter.value, objectContent: returnObjectContent.value },\n        index: props.index\n    };\n    emit(\"savedEventData\", eventData);\n}\n\nfunction addNewParameter() {\n    parameters.value.push({ parameterName: \"\" });\n}\n\nfunction getEventName(val: string) {\n    eventName.value = val;\n}\n\nfunction getReturnObjectContent(val: string) {\n    returnObjectContent.value = val;\n}\n\nfunction getParamObjectContent(val: string, index: number) {\n    parameters.value[index].parameterObjectContent = val;\n}\n\nfunction disableParamObjectContent(index: number) {\n    return parameters.value[index].pa