ing: 20px 24px;\n}\n.custom-card-header[data-v-5232f9d4] {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n    width: 100%;\n}\n.custom-card-name[data-v-5232f9d4] {\n    font-size: 20px;\n    font-weight: 500;\n    color: #212121;\n}\n.custom-card-description[data-v-5232f9d4] {\n    width: 100%;\n    height: 64px;\n    font-size: 14px;\n    color: #757575;\n    margin-top: 12px;\n    overflow-y: auto;\n}\n.custom-card-buttons[data-v-5232f9d4] {\n    display: flex;\n    justify-content: flex-start;\n    gap: 12px;\n    padding-top: 12px;\n    padding-bottom: 24px;\n}\n.edit-button[data-v-5232f9d4], .deactivate-button[data-v-5232f9d4], .activate-button[data-v-5232f9d4] {\n    display: flex;\n    align-items: center;\n    height: 32px;\n    border-radius: 4px;\n    font-size: 14px;\n    font-weight: 500;\n    cursor: pointer;\n    padding: 8px 16px;\n}\n.edit-button[data-v-5232f9d4] {\n    background-color: #FFFFFF;\n    color: #151233;\n    border: 1px solid #151233;\n}\n.deactivate-button[data-v-5232f9d4] {\n    background-color: #CBCBCB;\n    color: #151233;\n    border: none;\n}\n.activate-button[data-v-5232f9d4] {\n    background-color: #151233;\n    color: #FFFFFF;\n    border: none;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/IntegrationsSettings/integrationsMainPage.vue"],"names":[],"mappings":";AA+LA;IACI,eAAe;IACf,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,gCAAgC;AACpC;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,kBAAkB;IAClB,SAAS;IACT,iBAAiB;AACrB;AAEA;IACI,cAAc;IACd,gCAAgC;IAChC,iBAAiB;AACrB;AAEA;IACI,gBAAgB;IAChB,WAAW;AACf;AAEA;IACI,aAAa;IACb,eAAe;IACf,SAAS;IACT,2BAA2B;IAC3B,aAAa;IACb,WAAW;IACX,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,0BAA0B;IAC1B,aAAa;IACb,YAAY;AAChB;AAEA;IACI,yBAAyB;IACzB,eAAe;AACnB;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,gBAAgB;AACpB;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,kDAAkD;IAClD,kBAAkB;AACtB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,WAAW;AACf;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;AAEA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,cAAc;IACd,gBAAgB;IAChB,gBAAgB;AACpB;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,SAAS;IACT,iBAAiB;IACjB,oBAAoB;AACxB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,yBAAyB;AAC7B;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,YAAY;AAChB;AACA;IACI,yBAAyB;IACzB,cAAc;IACd,YAAY;AAChB","sourcesContent":["<template>\n    <div class=\"page-container\" v-if=\"!showCustomIntegrationPage && !showTokenIntegrationPage\">\n        <div class=\"main-description\">\n            <p>\n                This is the main page for integrations. Here you can see all the integrations that are available for you to\n                use. You can also add new integrations, edit existing ones, or delete them.\n            </p>\n        </div>\n        <div class=\"page-body\">\n            <div class=\"tabs-container\">\n                <!-- <div :class=\"'tab' + (showingTab === 'Easy Integrations' ? ' selected' : '')\"\n                    @click=\"selectedTab('Easy Integrations')\" style=\"width: 217px;\">\n                    <span>Easy Integrations</span>\n                </div> -->\n                <div :class=\"'tab' + (showingTab === 'Custom' ? ' selected' : '')\" @click=\"selectedTab('Custom')\"\n                    style=\"width: 157px;\">\n                    <span>Custom</span>\n                </div>\n                <div :class=\"'tab' + (showingTab === 'Token Integration' ? ' selected' : '')\" @click=\"selectedTab('Token Integration'), doesTokenExist()\"\n                    style=\"width: 218px;\">\n                    <span>Token Integration</span>\n                </div>\n            </div>\n            <div class=\"tab-content\">\n                <!-- <div v-if=\"showingTab === 'Easy Integrations'\">\n                    <p>Content for Easy Integrations tab goes here.</p>\n                </div> -->\n                <div v-if=\"showingTab === 'Custom'\" class=\"custom-content-container\">\n                    \n                    <div class=\"custom-integration-card\" v-for=\"(card, index) in customIntegrationCards\" :key=\"index\" v-if=\"customIntegrationCards.length > 0\">\n                        <div class=\"custom-card-header\">\n                            <img class=\"custom-integration-icon\" src=\"./customIntegrationIcon.svg\" alt=\"Custom Integration Icon\" />\n                            <span class=\"custom-card-name\">{{ card.name }}</span>\n                        </div>\n                        <div class=\"custom-card-description\">\n                            <span>{{ card.description }}</span>\n                        </div>\n                        <div class=\"custom-card-buttons\">\n                            <button class=\"edit-button\" @click=\"openSelectedCard(card, index)\">EDIT</button>\n                            <button class=\"deactivate-button\" @click=\"toggleActivation(card)\"> {{ card.isActivated ? 'DEACTIVATE' : 'ACTIVATE' }}</button>\n                            <button class=\"deactivate-button\" @click=\"deleteCard(index)\">DELETE</button>\n                        </div>\n                    </div>\n                    \n                    <div class=\"custom-add-card\" @click=\"openSelectedCard()\">\n                        <div class=\"add-card-content\">\n                            <i class=\"mdi mdi-plus add-card-icon\"></i>\n                            <span class=\"add-card-text\"><span style=\"color: #151233\">Add</span> Custom Integration</span>\n                        </div>\n                    </div>\n                </div>\n                <div v-if=\"showingTab === 'Token Integration'\">\n                    <div class=\"custom-integration-card\">\n                        <div class=\"custom-card-header\">\n                            <img class=\"custom-integration-icon\" src=\"./customIntegrationIcon.svg\" alt=\"Token Integration Icon\" />\n                            <span class=\"custom-card-name\">Token Integration</span>\n                        </div>\n                        <div class=\"custom-card-description\">\n                            <span>Token integration gives access to Studio Application Data for your third party products.</span>\n                        </div>\n                        <div class=\"custom-card-buttons\">\n                            <button v-if=\"!tokenExists\" class=\"activate-button\" @click=\"openTokenIntegration()\">ACTIVATE</button>\n                            <button v-if=\"tokenExists\" class=\"edit-button\" @click=\"openTokenDetails()\">DETAILS</button>\n                            <button v-if=\"tokenExists\" class=\"deactivate-button\" @click=\"deactivateToken()\">DEACTIVATE</button>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <div v-if=\"showCustomIntegrationPage\">\n        <CustomIntegrationPage :data=\"currentCard!\" :index=\"currentCard?.index\" @cancel=\"handleCancel\" @savedItemData=\"handleSavedItem\" :customIntegrationList=\"customIntegrationCards\"></CustomIntegrationPage>\n    </div>\n    <div v-if=\"showTokenIntegrationPage\">\n        <TokenIntegrationPage @cancel=\"handleTokenCancel\" :tokenValue=\"tokenIntegrationValue\"></TokenIntegrationPage>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref } from 'vue';\nimport CustomIntegrationPage from \"./customIntegrationPage.vue\";\nimport TokenIntegrationPage from \"./tokenIntegration.vue\";\nimport { ICustomIntegrationItem } from \"@stechquick/algae/lib/quick/IntegrationSettings\"\nimport { IUseCaseExecutor } from '../../../../../domain/useCase/IUseCaseExecutor';\nimport { container } from '../../../../../domain/core/diContainer';\nimport { IExternalTokenUseCase } from '../../../../../domain/useCase/IExternalTokenUseCase';\nimport { IViewModel } from '../../../../../domain/viewModel/IViewModel';\nimport { exitCode } from 'process';\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst externalTokenUseCase = container.resolve<IExternalTokenUseCase>(IExternalTokenUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\n\nconst emit = defineEmits([\"hideLeftNavAndHeader\", \"showLeftNavAndHeader\", \"customIntegrationsUpdated\"]);\nconst props = defineProps<{ cardData: Array<ICustomIntegrationItem>; }>();\nconst currentAppId = ref(viewModel.studio.appId);\nlet currentCard = ref<ICustomIntegrationItem>();\nconst showingTab = ref(<\"Easy Integrations\" | \"Custom\" | \"Token Integration\">(\"Custom\"));\nlet showCustomIntegrationPage = ref(false);\nlet showTokenIntegrationPage = ref(false);\nconst customIntegrationCards = ref<Array<ICustomIntegrationItem>>(props.cardData);\nconst tokenIntegrationValue = ref(\"\");\nconst tokenExists = ref(false);\n\nfunction openSelectedCard(card?: ICustomIntegrationItem, index?: number) {\n    if (card) {\n        currentCard.value = Object.assign({}, card, { index });\n    } else {\n        currentCard.value = {\n            name: \"\",\n            loadOption: {\n                loadOnce: true,\n                loadEverytime: false\n            },\n        };\n    }\n    emit(\"hideLeftNavAndHeader\");\n    showCustomIntegrationPage.value = true;\n}\n\nfunction toggleActivation(card: ICustomIntegrationItem) {\n    card.isActivated = !card.isActivated; \n    emit(\"customIntegrationsUpdated\", customIntegrationCards.value); //ATA: THIS MAY CHANGE IN THE FUTURE\n}\n\nfunction deleteCard(index: number) {\n    customIntegrationCards.value.splice(index, 1);\n    emit(\"customIntegrationsUpdated\", customIntegrationCards.value); //ATA: THIS MAY CHANGE IN THE FUTURE\n}\n\nfunction selectedTab(val: \"Easy Integrations\" | \"Custom\" | \"Token Integration\") {\n    showingTab.value = val;\n}\n\nfunction handleCancel() {\n    showCustomIntegrationPage.value = false;\n    emit(\"showLeftNavAndHeader\")\n}\nfunction openTokenIntegration() {\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n    const currentAppId = viewModel.studio.appId;\n    executor.execute(async () => {\n        const tokenResp  = await externalTokenUseCase.generateShareToken(currentAppId);\n        tokenIntegrationValue.value = tokenResp.token;\n        openTokenDetails();\n    }, {loading: true})\n}\nfunction handleTokenCancel() {\n    showTokenIntegrationPage.value = false;\n    emit(\"showLeftNavAndHeader\");\n}\nfunction openTokenDetails() {\n    doesTokenExist();\n    emit(\"hideLeftNavAndHeader\");\n    showTokenIntegrationPage.value = true;\n}\nfunction doesTokenExist() {\n    executor.execute(async () => {\n        const extToken = await externalTokenUseCase.getAppActiveToken(currentAppId.value);\n        if(extToken.tokenCount > 0) { \n            tokenExists.value = true;\n            if(extToken.token) { tokenIntegrationValue.value = extToken.token; } \n        }\n        else {\n            tokenExists.value = false;\n        }\n    }, {loading: true})\n}\nfunction deactivateToken() {\n    executor.execute(async () => {\n       await externalTokenUseCase.deactivateToken(currentAppId.value);\n       doesTokenExist();\n    }, {loading: true})\n}\n\nfunction handleSavedItem(savedItem: ICustomIntegrationItem) { \n    if (savedItem.index !== undefined) { // !== undefined because when index is 0 results in false \n        customIntegrationCards.value.splice(savedItem.index, 1, savedItem);\n    } else {\n        savedItem.isActivated = true;\n        savedItem.index = customIntegrationCards.value.length;\n        customIntegrationCards.value.push(savedItem);\n    }\n    showCustomIntegrationPage.value = false;\n    emit(\"customIntegrationsUpdated\", customIntegrationCards.value) //ATA: THIS MAY CHANGE IN THE FUTURE\n    emit(\"showLeftNavAndHeader\");\n}\n\n</script>\n\n<style scoped>\n.main-description {\n    font-size: 16px;\n    line-height: 19px;\n    text-align: left;\n    color: #616161;\n    font-weight: 400;\n}\n\n.page-body {\n    margin-top: 32px;\n}\n\n.tabs-container {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    width: 100%;\n    height: 36px;\n    border-bottom: 1px solid #bdbdbd;\n}\n\n.tab {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100%;\n    font-size: 14px;\n    line-height: 17px;\n    color: #A1A1A1;\n    font-weight: 500;\n    cursor: pointer;\n    padding: 10px 54px;\n    gap: 10px;\n    user-select: none;\n}\n\n.tab.selected {\n    color: #151233;\n    border-bottom: 2px solid #151233;\n    user-select: none;\n}\n\n.tab-content {\n    margin-top: 24px;\n    width: 100%;\n}\n\n.custom-content-container {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 12px;\n    justify-content: flex-start;\n    height: 440px;\n    width: 100%;\n    overflow-y: auto;\n}\n\n.custom-add-card {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    border-radius: 10px;\n    border: 1px dashed #BCBCBC;\n    height: 212px;\n    width: 454px;\n}\n\n.custom-add-card:hover {\n    border: 1px solid #151233;\n    cursor: pointer;\n}\n\n.add-card-content {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.add-card-icon {\n    font-size: 24px;\n    margin-right: 3px;\n}\n\n.add-card-text {\n    font-size: 16px;\n    line-height: 19px;\n    color: #BCBCBC;\n    font-weight: 400;\n}\n\n.custom-integration-card {\n    width: 454px;\n    height: 212px;\n    border-radius: 10px;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px 0px rgba(33, 33, 33, 0.16);\n    padding: 20px 24px;\n}\n\n.custom-card-header {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n    width: 100%;\n}\n\n.custom-card-name {\n    font-size: 20px;\n    font-weight: 500;\n    color: #212121;\n}\n\n.custom-card-description {\n    width: 100%;\n    height: 64px;\n    font-size: 14px;\n    color: #757575;\n    margin-top: 12px;\n    overflow-y: auto;\n}\n\n.custom-card-buttons {\n    display: flex;\n    justify-content: flex-start;\n    gap: 12px;\n    padding-top: 12px;\n    padding-bottom: 24px;\n}\n\n.edit-button, .deactivate-button, .activate-button {\n    display: flex;\n    align-items: center;\n    height: 32px;\n    border-radius: 4px;\n    font-size: 14px;\n    font-weight: 500;\n    cursor: pointer;\n    padding: 8px 16px;\n}\n\n.edit-button {\n    background-color: #FFFFFF;\n    color: #151233;\n    border: 1px solid #151233;\n}\n\n.deactivate-button {\n    background-color: #CBCBCB;\n    color: #151233;\n    border: none;\n}\n.activate-button {\n    background-color: #151233;\n    color: #FFFFFF;\n    border: none;\n}\n\n</style>\n"],"sourceRoot":""}]);
