f<{ [key: string]: any }>({})\nfunction onBackClick() {\n    emit(\"onBack\");\n}\n\nfunction onColorChange(value: string, colorName: string) {\n    const selectedColor = props.theme.colors.find(color => color.name == colorName)!;\n    selectedColor.color = value;\n}\n\nfunction addNewColor() {\n    const nameIndex = customColors.value.length;\n    const generateRandomColor = () => {\n        const randomInt = Math.floor(Math.random() * 16777216);\n        const hexColor = `#${randomInt.toString(16).padStart(6, '0')}`;\n        return hexColor;\n    };\n    const color = generateRandomColor();\n\n    const customColor: ThemeColor = { name: \"DefaultColor\" + nameIndex, allowGradient: true, color }\n    customColors.value.push(customColor);\n    props.theme.colors.push(customColor);\n}\n\nfunction onColorCardClick(colorName: string) {\n    (<any>colorInputs.value[colorName])?.clickColorPicker();\n}\n\nfunction onSaveTheme() {\n    emit(\"onSaveTheme\", props.theme);\n}\n\nfunction assignRefs(colorName: string, element: any) {\n    colorInputs.value[colorName] = element\n}\nfunction onCustomColorDelete(colorName: string) {\n    const colorIndex = customColors.value.findIndex(color => color.name == colorName)!\n    customColors.value.splice(colorIndex, 1);\n    props.theme.colors.splice(10 + colorIndex, 1);\n}\n\nfunction calcPickerStyle(index: number) {\n    return { left: index > 7 ? \"-210px\" : \"20px\", top: \"-80px\" }\n}\n\nfunction isColorValid(colorCode: string) {\n    const regex = /^#[0-9A-Fa-f]{3}$|^#[0-9A-Fa-f]{6}$/i;\n    const isValidColorCode = regex.test(colorCode);\n    return isValidColorCode;\n}\n\nfunction onColorInputChange(input: string, colorValue: string) {\n    const isInputValid = isColorValid(input);\n    if (!isInputValid) {\n        colorValue = \"#000000\";\n    }\n}\n</script>\n\n<template>\n    <div class=\"config-div\">\n        <div class=\"config-tab\">\n            <div @click=\"activeTab = TabState.Color;\"\n                :class=\"['tab-item', (activeTab == TabState.Color ? 'active-category' : '')]\">Color</div>\n        </div>\n        <template v-if=\"activeTab == TabState.Color\">\n            <div class=\"defined-colors\">\n                <div class=\"color-card\" v-for=\"(color, index) in props.theme.colors.slice(0, 10)\" :key=\"color.name\">\n                    <span class=\"color-name\">{{ color.name }}</span>\n                    <div @click=\"onColorCardClick(color.name)\">\n                        <div class=\"color\" :style=\"{ backgroundColor: color.color, cursor: 'pointer' }\"></div>\n                        <div></div>\n                    </div>\n                    <input class=\"color-input-card color-input\" type=\"text\" v-model=\"color.color\"\n                        @change=\"onColorChange($event, color.color)\">\n                    <ColorInput :color=\"color.color\" :showColorDiv=\"false\" :pickerStyle=\"calcPickerStyle(index)\"\n                        @onChangeColor=\"onColorChange($event, color.name)\" :ref=\"el => assignRefs(color.name, el)\">\n                    </ColorInput>\n                </div>\n            </div>\n            <div class=\"other-colors\">\n                <span class=\"other-title\">Other Colors</span>\n                <div class=\"custom-color-div\" v-for=\"color in  customColors \" :key=\"color.name\">\n                    <input type=\"text\" class=\"color-input custom-color-name\" v-model=\"color.name\">\n                    <input type=\"text\" class=\"custom-color color-input\" v-model=\"color.color\">\n                    <ColorInput :color=\"color.color\" :showColorDiv=\"true\" :pickerStyle=\"{ left: '-200px' }\"\n                        @onChangeColor=\"onColorChange($event, color.name)\" :ref=\"el => assignRefs(color.name, el)\">\n                    </ColorInput>\n                    <i class=\"delete-icon mdi mdi-delete-outline\" @click=\"onCustomColorDelete(color.name)\"></i>\n                </div>\n                <button class=\"add-color-btn\" style=\"font-size: 16px\" @click=\"addNewColor\">\n                    <i class=\"mdi mdi-plus add-theme-icon\"></i>\n                    Add new color\n                </button>\n\n            </div>\n        </template>\n\n        <template v-if=\"activeTab == TabState.Fonts\">\n        </template>\n        <!-- <div class=\"navigations\">\n            <button class=\"back-button\" @click=\"onBackClick\">Back</button>\n            <button class=\"save-button\" @click=\"onSaveTheme\">SAVE</button>\n        </div> -->\n    </div>\n</template>\n\n<style scoped>\n.custom-color {\n    width: 90px;\n}\n\n.delete-icon {\n    font-family: Material Design Icons;\n    font-size: 16px;\n    font-weight: 400;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n    color: #757575;\n    margin-left: 6px;\n    cursor: pointer;\n}\n\n.custom-color-name {\n    width: 918px;\n}\n\n.color-input {\n    font-family: Roboto;\n    font-size: 12px;\n    font-weight: 400;\n    line-height: 14px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #212121;\n    padding: 7px 6px 7px 6px;\n    border-radius: 4px;\n    border: 1px solid #BDBDBD;\n    height: 28px;\n}\n\n.custom-color-div {\n    display: flex;\n    gap: 12px;\n    margin-bottom: 12px;\n    align-items: center;\n}\n\n.save-button {\n    width: 72px;\n    height: 40px;\n    padding: 8px, 16px, 8px, 16px;\n    border-radius: 4px;\n    gap: 4px;\n    background: #151233;\n    font-family: Roboto;\n    font-size: 16px;\n    font-weight: 500;\n    line-height: 19px;\n    letter-spacing: 0px;\n    text-align: center;\n    color: #FFFFFF;\n\n}\n\n.back-button {\n    width: 68px;\n    height: 40px;\n    padding: 8px 16px 8px 16px;\n    gap: 4px;\n    font-family: Roboto;\n    font-size: 16px;\n    font-weight: 500;\n    line-height: 19px;\n    letter-spacing: 0px;\n    text-align: center;\n    color: #757575;\n    background-color: transparent;\n    border: none;\n}\n\n.navigations {\n    position: absolute;\n    bottom: -200px;\n    right: 0;\n}\n\n.other-colors {\n    display: flex;\n    flex-direction: column;\n}\n\n.add-color-btn {\n    height: 32px;\n    padding: 8px 16px 8px 16px;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0px;\n    text-align: center;\n    color: #757575;\n    background: transparent;\n    border: none;\n    margin: auto;\n}\n\n.other-title {\n    font-family: Roboto;\n    font-size: 15px;\n    font-weight: 500;\n    line-height: 18px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #151233;\n    margin-bottom: 12px;\n}\n\n.tab-item {\n    width: 149px;\n    height: 36px;\n    padding: 10px 54px 10px 54px;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n    color: #A1A1A1;\n\n}\n\n.defined-colors {\n    display: flex;\n    gap: 14px;\n    margin: 24px 0;\n}\n\n.color-card {\n    width: 100px;\n    height: 158px;\n    padding: 12px;\n    border-radius: 8px;\n    background: #F5F5F5;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n\n.color-input-card {\n    width: 72px;\n}\n\n.color {\n    width: 72px;\n    height: 72px;\n    border-radius: 2px;\n    margin: 10px 0;\n}\n\n.color-name {\n    font-family: Roboto;\n    font-size: 12px;\n    font-weight: 400;\n    line-height: 14px;\n    letter-spacing: 0em;\n    text-align: center;\n    color: #212121;\n}\n\n.config-tab {\n    display: flex;\n    align-items: center;\n    margin: 32px 0 0;\n    border-bottom: 1px solid var(--grey-lighten-1, #BDBDBD);\n}\n\n.active-category {\n    color: #151233;\n    border-bottom: 2px solid;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n}\n</style>"],"sourceRoot":""}]);
