ht: 24px;\n  letter-spacing: 0em;\n  text-align: center;\n  margin-right: 10px;\n}\n.slide-fade-enter-active[data-v-4d5411bf] {\n  transition: all 0.2s ease-out;\n}\n.slide-fade-leave-active[data-v-4d5411bf] {\n  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);\n}\n.slide-fade-enter-from[data-v-4d5411bf],\n.slide-fade-leave-to[data-v-4d5411bf] {\n  transform: translateX(20px);\n  opacity: 0;\n}\n.theme-desc[data-v-4d5411bf] {\n  font-size: 16px;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: left;\n  color: #616161;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/ThemesSettings.vue"],"names":[],"mappings":";AAyKA;EACE,aAAa;AACf;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,kBAAkB;AACpB;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,mBAAmB;EACnB,cAAc;EACd,gBAAgB;EAChB,kBAAkB;AACpB;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,sBAAsB;EACtB,0BAA0B;EAC1B,gBAAgB;EAChB,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,cAAc;EACd,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,kBAAkB;EAClB,eAAe;AACjB;AAEA;EACE,WAAW;EACX,YAAY;EACZ,6BAA6B;EAC7B,kBAAkB;EAClB,QAAQ;EACR,mBAAmB;EACnB,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,kBAAkB;EAClB,cAAc;AAEhB;AAEA;EACE,WAAW;EACX,YAAY;EACZ,0BAA0B;EAC1B,QAAQ;EACR,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,kBAAkB;EAClB,cAAc;EACd,6BAA6B;EAC7B,YAAY;AACd;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,iBAAiB;AACnB;AAEA;EACE,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,gBAAgB;EAChB,gBAAgB;EAChB,kBAAkB;EAClB,cAAc;AAChB;AAEA;EACE,iBAAiB;EACjB,cAAc;AAChB;AAEA;EACE,kCAAkC;EAClC,eAAe;EACf,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,kBAAkB;EAClB,kBAAkB;AACpB;AAEA;EACE,6BAA6B;AAC/B;AAEA;EACE,iDAAiD;AACnD;AAEA;;EAEE,2BAA2B;EAC3B,UAAU;AACZ;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,mBAAmB;EACnB,gBAAgB;EAChB,cAAc;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { ITheme, ThemeColor } from \"@stechquick/algae/lib/quick/IPlateauUI\";\nimport { computed, ref } from \"vue\";\nimport { IAppSettings } from \"../../../../../../common/qCloudTemp/application\";\nimport ThemeCard from \"./Theme/ThemeCard.vue\";\nimport ThemeConfiguration from \"./Theme/ThemeConfiguration.vue\";\n\nenum ShowState {\n  ThemeNames = 1,\n  ThemeCards = 2,\n  ThemeConfiguration = 3\n}\nconst DEFAULTTHEMENAME = \"Theme1\";\ninterface IThemeSettingsProp {\n  themeSettings: IAppSettings['themeSettings']\n}\nconst props = defineProps<IThemeSettingsProp>();\nconst emit = defineEmits([\"onSave\", \"hideLeftNav\", \"showLeftNav\", \"showHeader\", \"hideHeader\"]);\nconst themeDisplayState = ref(ShowState.ThemeNames);\nconst selectedThemeName = ref(props.themeSettings?.defaultTheme || DEFAULTTHEMENAME);\nconst editTheme = ref<ITheme>({ name: \"\", colors: [] });\nconst title = ref(\"Themes Settings\")\n\n\nfunction generateDefaultTheme() {\n  const preDefinedColors: Array<ThemeColor> = ([{ name: \"Background\", allowGradient: true, color: \"#FFFFFF\" },\n  { name: \"Text\", allowGradient: false, color: \"#212121\" },\n  { name: \"Primary\", allowGradient: false, color: \"#151233\" },\n  { name: \"Secondary\", allowGradient: false, color: \"#FD454C\" },\n  { name: \"Tertiary\", allowGradient: false, color: \"#007C77\" },\n  { name: \"Passive\", allowGradient: false, color: \"#7A95B0\" },\n  { name: \"Info\", allowGradient: false, color: \"#03A9F4\" },\n  { name: \"Success\", allowGradient: false, color: \"#00BB41\" },\n  { name: \"Warning\", allowGradient: false, color: \"#F3CA40\" },\n  { name: \"Error\", allowGradient: false, color: \"#F44336\" }]);\n  const defaultTheme: ITheme = { name: DEFAULTTHEMENAME, colors: preDefinedColors }\n  return defaultTheme;\n}\n\nconst themes = ref(props.themeSettings?.themes || [generateDefaultTheme()]);\n\nconst colors = computed(() => {\n  const selectedThemeColors: Array<ThemeColor> = themes.value.find(theme => theme.name == selectedThemeName.value)!.colors;\n  return selectedThemeColors;\n})\n\n\nfunction changeDisplay(state: number) {\n  themeDisplayState.value += state\n  switch (themeDisplayState.value) {\n    case ShowState.ThemeCards:\n      title.value = \"Themes\"\n      emit(\"hideLeftNav\")\n      break;\n    case ShowState.ThemeNames:\n      title.value = \"Themes Settings\";\n      emit(\"showLeftNav\");\n      break;\n    case ShowState.ThemeConfiguration:\n      title.value = \"Theme Configuration\";\n      break;\n    default:\n      break;\n  }\n  emit(\"hideHeader\")\n}\n\nfunction findNewString(themeName: string) {\n  let emptyNumber = themes.value.map(theme => {\n    let match = theme.name.match(/Theme(\\d+)$/);\n    return match ? parseInt(match[1]) : 0;\n  });\n\n  let numberToBeAdd: any = themeName.match(/\\d+$/);\n  numberToBeAdd = numberToBeAdd ? parseInt(numberToBeAdd[0]) : 1;\n\n  while (emptyNumber.includes(numberToBeAdd)) {\n    numberToBeAdd++;\n  }\n  return `Theme${numberToBeAdd}`;\n}\n\n\nfunction addNewTheme() {\n  const defaultTheme = generateDefaultTheme();\n\n  let defaultName = \"Theme\";\n  const themeName = findNewString(defaultName);\n  themes.value.push({ name: themeName, colors: defaultTheme.colors });\n}\n\nfunction onEditTheme(themeName: string) {\n  editTheme.value = themes.value.find(theme => theme.name == themeName)!;\n  changeDisplay(1);\n}\n\nfunction setDefaultTheme(themeName: string) {\n  selectedThemeName.value = themeName;\n}\n\nfunction onSaveTheme() {\n  emit(\"onSave\", { defaultTheme: selectedThemeName.value, themes: themes.value })\n}\n\nfunction deleteTheme(themeName: string) {\n  const deleteThemeIndex = themes.value.findIndex(theme => theme.name == themeName)!;\n  const deletedThemeName = themes.value[deleteThemeIndex].name\n  themes.value.splice(deleteThemeIndex, 1);\n  if (deletedThemeName == selectedThemeName.value) {\n    selectedThemeName.value = themes.value[0].name;\n  }\n}\n\n</script>\n\n<template>\n  <div>\n    <div class=\"theme-content\">\n      <div class=\"title\">{{ title }}</div>\n      <div class=\"theme-desc\">\n        <span>Some Text here to describe</span>\n      </div>\n      <div v-if=\"themeDisplayState == ShowState.ThemeNames\" class=\"default-title\">\n        <span>Default Themes</span>\n      </div>\n      <Transition name=\"slide-fade\">\n        <div class=\"row\" v-if=\"themeDisplayState == ShowState.ThemeNames\">\n          <div class=\"col-6\">\n            <ThemeCard :theme=\"{ name: selectedThemeName, colors }\" :showDeleteIcon=\"false\" :showRadioIcon=\"false\"\n              :selectedThemeName=\"selectedThemeName\" @cardClick=\"changeDisplay(1)\" @pencilClick.stop @deleteClick.stop\n              @radioClick.stop>\n            </ThemeCard>\n          </div>\n        </div>\n      </Transition>\n      <Transition name=\"slide-fade\">\n        <div v-if=\"themeDisplayState == ShowState.ThemeCards\" class=\"row\">\n          <div v-for=\"theme in themes\" :key=\"theme.name\" class=\"col-6\" style=\"margin-top: 12px;\">\n            <ThemeCard :theme=\"theme\" :showRadioIcon=\"true\" :showDeleteIcon=\"themes.length > 1 ? true : false\"\n              :selectedThemeName=\"selectedThemeName\" @cardClick=\"setDefaultTheme(theme.name)\"\n              @pencilClick=\"onEditTheme(theme.name)\" @deleteClick=\"deleteTheme(theme.name)\"\n              @radioClick=\"setDefaultTheme(theme.name)\">\n            </ThemeCard>\n          </div>\n          <div class=\"col-6\">\n            <div class=\"add-theme-card\" @click=\"addNewTheme\">\n              <i class=\"mdi mdi-plus add-theme-icon\"></i>\n              <span class=\"add-text\">Add</span>\n              <span>new theme</span>\n            </div>\n          </div>\n        </div>\n      </Transition>\n      <Transition name=\"slide-fade\">\n        <div v-if=\"themeDisplayState == ShowState.ThemeConfiguration\" class=\"themes-configuration\">\n          <ThemeConfiguration :theme=\"editTheme\" @onBack=\"changeDisplay(-1)\" @onSaveTheme=\"onSaveTheme\">\n          </ThemeConfiguration>\n        </div>\n      </Transition>\n    </div>\n\n    <div v-if=\"themeDisplayState != ShowState.ThemeNames\" class=\"navigations\">\n      <button class=\"back-button\" @click=\"changeDisplay(-1)\">Back</button>\n      <button class=\"save-button\" @click=\"onSaveTheme\">SAVE</button>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.theme-content::-webkit-scrollbar {\n  display: none;\n}\n\n.theme-content {\n  height: 620px;\n  overflow-y: scroll;\n  overflow-x: hidden;\n}\n\n.title {\n  font-style: normal;\n  font-weight: 500;\n  font-size: 20px;\n  line-height: 23px;\n  white-space: nowrap;\n  color: #151233;\n  margin-top: 40px;\n  margin-bottom: 8px;\n}\n\n.add-theme-card {\n  display: flex;\n  align-items: center;\n  vertical-align: middle;\n  border: 1px dashed #BCBCBC;\n  margin-top: 12px;\n  height: 115px;\n  justify-content: center;\n  border-radius: 10px;\n  color: #BCBCBC;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: center;\n  cursor: pointer;\n}\n\n.save-button {\n  width: 72px;\n  height: 40px;\n  padding: 8px, 16px, 8px, 16px;\n  border-radius: 4px;\n  gap: 4px;\n  background: #151233;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #FFFFFF;\n\n}\n\n.back-button {\n  width: 68px;\n  height: 40px;\n  padding: 8px 16px 8px 16px;\n  gap: 4px;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #757575;\n  background-color: transparent;\n  border: none;\n}\n\n.navigations {\n  display: flex;\n  justify-content: flex-end;\n  padding-top: 24px;\n}\n\n.default-title {\n  font-family: Roboto;\n  font-size: 15px;\n  font-weight: 500;\n  line-height: 18px;\n  letter-spacing: 0em;\n  text-align: left;\n  margin-top: 24px;\n  margin-bottom: 8px;\n  color: #151233;\n}\n\n.add-text {\n  margin-right: 4px;\n  color: #151233;\n}\n\n.add-theme-icon {\n  font-family: Material Design Icons;\n  font-size: 24px;\n  font-weight: 400;\n  line-height: 24px;\n  letter-spacing: 0em;\n  text-align: center;\n  margin-right: 10px;\n}\n\n.slide-fade-enter-active {\n  transition: all 0.2s ease-out;\n}\n\n.slide-fade-leave-active {\n  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);\n}\n\n.slide-fade-enter-from,\n.slide-fade-leave-to {\n  transform: translateX(20px);\n  opacity: 0;\n}\n\n.theme-desc {\n  font-size: 16px;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: left;\n  color: #616161;\n}\n</style>"],"sourceRoot":""}]);
