g = container.resolve<IDialog>(IDialog);\nconst applicationVersionUseCase = container.resolve<IApplicationVersionUseCase>(IApplicationVersionUseCase);\n\ninterface IProps {\n  functions: IAppSettingsEditorProps[\"functions\"];\n  callbacks: IAppSettingsEditorProps[\"callbacks\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst organizationSettingsUseCase = container.resolve<IOrganizationSettingsUseCase>(IOrganizationSettingsUseCase);\n\nconst props = defineProps<IProps>();\nconst settingsModel = ref<IAppSettings>();\nlet oldSettingsModel: IAppSettings = { general: {} };\nlet newSettingsModel: IAppSettings = { general: {} };\nconst showingTab = ref(<\"General\" | \"Themes\" | \"oAuth\" | \"Integrations\" | \"App Versions\">(\"General\"));\nconst app = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\n\nconst appVersions = ref<{ [key: string]: any }>({});\nconst createAppVersionPage = ref(false)\n\nconst oAuthSettingsModel = computed(() => {\n  if (settingsModel.value!.oAuthSettings) {\n    return settingsModel.value!.oAuthSettings;\n  }\n  let settingsIAMV2: ISettingsIAMV2 = { active: false, idendityProviderURL: '', clientId: '', realm: '', isRealmFromQS: undefined, language: '', isLangFromQS: undefined, subdomainSource: undefined, subdomainValue: '', unsecurePages: [''], redirectUri: '', preserveTokens: false }\n  return settingsIAMV2;\n})\n\nconst themeSettings = computed(() => settingsModel.value?.themeSettings)\n\nconst defaultWebLottieName = computed(() => {\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.webLoading;\n  if (lottiePath && lottiePath.length > 0) {\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    return retVal;\n  }\n  return \"\";\n})\n\nconst defaultMobileLottieName = computed(() => {\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.mobileLoading;\n  if (lottiePath && lottiePath.length > 0) {\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    return retVal;\n  }\n  return \"\";\n})\n\nconst modelMap = computed(() => viewModel.studio.items.map(model => {\n  return { ID: model.ID, name: model.name, type: model.objectType };\n}))\n\nfunction inputChanged() {\n  if (newSettingsModel.general.mobileLoading == oldSettingsModel.general.mobileLoading && newSettingsModel.general.siteTitle == oldSettingsModel.general.siteTitle &&\n    newSettingsModel.general.startScreen == oldSettingsModel.general.startScreen && newSettingsModel.general.webLoading == oldSettingsModel.general.webLoading &&\n    newSettingsModel.oAuthSettings == oldSettingsModel.oAuthSettings && newSettingsModel.themeSettings == oldSettingsModel.themeSettings && newSettingsModel.integrationsSettings === oldSettingsModel.integrationsSettings)\n    props.callbacks.modified({ model: false });\n  else\n    props.callbacks.modified({ model: true });\n}\n\nprops.functions.getModel = async () => {\n  const model = JSON.stringify(settingsModel.value!);\n  return { model, state: {} };\n};\nprops.functions.setModel = async (model, state) => {\n  settingsModel.value = JSON.parse(model);\n  oldSettingsModel = JSON.parse(model);\n  newSettingsModel = JSON.parse(model);\n}\n\nconst lottiesModels = computed(() => {\n  const models: Array<String> = [];\n  viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n    if (item.objectType == \"model\" && item.modelType == \"lottie\") { models.push(item.name); }\n    return false;\n  });\n  return models;\n});\n\nfunction updateApp() {\n  let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId) as IApplication;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../components/application/addOrUpdateApp.vue`)),\n    { closable: true, title: \"Update Application\" },\n    { app: appParam }\n  );\n}\n\nfunction changeAppLogo() {\n  let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId);\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../components/dialogs/appLogoDialog.vue`)),\n    { closable: true, title: \"Select App Image\", width: \"530px\" },\n    { app: appParam }\n  );\n}\n\nfunction onWebLottieSelect(lottieName: string) {\n  // settingsModel.value!.general.webLoading = lottieName;\n  const modelID = getModelIDInModelMapByName(lottieName);\n  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n  settingsModel.value!.general.webLoading = val;\n  newSettingsModel.general.webLoading = val;\n  inputChanged();\n}\n\nfunction onMobileLottieSelect(lottieName: string) {\n  const modelID = getModelIDInModelMapByName(lottieName);\n  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n  settingsModel.value!.general.mobileLoading = val;\n  newSettingsModel.general.mobileLoading = val;\n  inputChanged();\n}\n\nfunction onInputSiteTitle($event: any) {\n  settingsModel.value!.general.siteTitle = $event.target.value;\n  newSettingsModel.general.siteTitle = $event.target.value;\n  inputChanged();\n}\nfunction onOAuthSettingsChange(params: { [key: string]: string }) {\n  const changedKey = Object.keys(params)[0];\n\n  (<any>oAuthSettingsModel.value)[changedKey] = params[changedKey];\n  settingsModel.value!.oAuthSettings = oAuthSettingsModel.value;\n  newSettingsModel.oAuthSettings = oAuthSettingsModel.value;\n  if (newSettingsModel.oAuthSettings.unsecurePages?.[0] == \"\") {\n    delete newSettingsModel.oAuthSettings.unsecurePages;\n  }\n  inputChanged();\n}\n\nfunction importDialog() {\n  dialog.showDialog<{ param: INewItemPropsInline }>(\n    asyncComponentCreator.createInputModelDialog(),\n    { closable: true, title: \"Upload Local File\", closeOnOutClick: true, maxHeight: \"487px\", width: \"600px\" },\n    { param: { ownerId: viewModel.studio.appId, path: \"/\" } },\n  );\n}\n\nfunction addEventListeners() {\n  document.getElementById(\"add-web-lottie\")!.addEventListener(\"click\", importDialog);\n  document.getElementById(\"add-mobile-lottie\")!.addEventListener(\"click\", importDialog);\n}\n\nfunction selectTab(val: \"General\" | \"Themes\" | \"oAuth\" | \"Integrations\" | \"App Versions\") {\n  showingTab.value = val;\n  if (val != \"Themes\" && val != \"App Versions\") {\n    showHeader();\n  }\n}\n\nfunction getModelNameInModelMapByID(ID?: string) {\n  return modelMap.value.find(i => i.ID == ID)?.name;\n}\n\nfunction getModelIDInModelMapByName(name?: string) {\n  return modelMap.value.find(i => i.name == name)?.ID;\n}\n\nconst isLeftNavHidden = ref(false);\nconst isHeaderHidden = ref(false);\n\nfunction hideLeftNavAndHeader() {\n  isLeftNavHidden.value = true;\n  isHeaderHidden.value = true;\n}\n\nfunction showLeftNavAndHeader() {\n  isLeftNavHidden.value = false;\n  isHeaderHidden.value = false;\n}\n\nfunction hideLeftNav() {\n  isLeftNavHidden.value = true;\n}\n\nfunction showLeftNav() {\n  isLeftNavHidden.value = false;\n}\n\nfunction hideHeader() {\n  isHeaderHidden.value = true;\n}\n\nfunction showHeader() {\n  isHeaderHidden.value = false;\n}\n\nfunction onThemeSave({ defaultTheme, themes }: { defaultTheme: string, themes: Array<ITheme> }) {\n  newSettingsModel.themeSettings = { defaultTheme, themes };\n  settingsModel.value!.themeSettings = newSettingsModel.themeSettings;\n  inputChanged();\n}\n\nfunction handleCustomIntegrationsUpdate(customIntegrationCards: Array<ICustomIntegrationItem>) {\n  if (settingsModel.value?.integrationsSettings && newSettingsModel.integrationsSettings) {\n    settingsModel.value!.integrationsSettings.customIntegrations = customIntegrationCards;\n    newSettingsModel.integrationsSettings.customIntegrations = customIntegrationCards;\n    inputChanged();\n    return;\n  }\n  settingsModel.value!.integrationsSettings = { customIntegrations: customIntegrationCards };\n  newSettingsModel.integrationsSettings = { customIntegrations: customIntegrationCards };\n  inputChanged();\n}\n\nfunction getCardData() {\n  return oldSettingsModel.integrationsSettings?.customIntegrations ?? [];\n}\nfunction checkAppVersionPermission() {\n  const org = viewModel.user?.selectedOrganization;\n  const orgGroup = viewModel.user?.selectedOrganizationGroup?.organizationGroup;\n  const isAppVersionAllowed = organizationSettingsUseCase.isAppVersionAllowed(org!, orgGroup!);\n  return isAppVersionAllowed;\n  \n}\nfunction toggleAppVersionPages() {\n  createAppVersionPage.value =  !createAppVersionPage.value;\n}\nfunction assignRefs(appName: string | undefined, element: any) {\n  if(!appName) {return };\n  appVersions.value[appName] = element\n}\nfunction listtAppVersions() {\n  (<any>appVersions.value[app.value!.name])?.listAppVersions();\n}\nfunction deleteAppVersion(appVersionId: string) {\n      executor.execute(async () => {\n          const resp = await applicationVersionUseCase.deleteApplicationVersion({versionID: appVersionId});\n          if(resp != false) { listtAppVersions() };\n      }, {loading: true})\n}\n</script>\n\n\n<template>\n  <div class=\"maindiv\">\n    <div class=\"leftnav\" v-if=\"!isLeftNavHidden\">\n      <div :class=\"'leftnavbutton' + (showingTab === 'General' ? ' selected' : '')\" @click=\"selectTab('General')\">\n        <i class=\"mdi mdi-cogs leftnavbuttonicon\"></i>\n        <span :class=\"'leftnavbuttontext' + (showingTab === 'General' ? ' selected' : '')\">General</span>\n      </div>\n      <div :class=\"'leftnavbutton' + (showingTab === 'Themes' ? ' selected' : '')\" @click=\"selectTab('Themes')\">\n        <i class=\"mdi mdi-brush-outline leftnavbuttonicon\"></i>\n        <span :class=\"'leftnavbuttontext' + (showingTab === 'Themes' ? ' selected' : '')\">Themes</span>\n      </div>\