:app=\"app\" :showTitleAndDesc=\"true\" \n            @hideLeftNav=\"hideLeftNav\" @showLeftNav=\"showLeftNav\" @showHeader=\"showHeader\" @hideHeader=\"hideHeader\"\n            :rowActionButtons=\"[{icon: 'mdi mdi-delete-outline', callBack: deleteAppVersion}]\"\n            :addButton=\"{buttonText: 'ADD NEW', callBack: toggleAppVersionPages}\">\n          </AppVersions>\n          <CreateAppVersion v-if=\"createAppVersionPage\" \n            :app=\"app\" :showTitleAndDesc=\"true\" \n            @hideLeftNav=\"hideLeftNav\" @showLeftNav=\"showLeftNav\" @close=\"toggleAppVersionPages\">\n          </CreateAppVersion>\n\n        </div>\n      </div>\n    </div>\n    <!--<div class=\"footer\">\n       <div class=\"reset\">Reset</div>\n      <div class=\"savechanges\">SAVE CHANGES</div> \n    </div>-->\n  </div>\n</template>\n\n\n\n<style scoped>\n.right-section-container {\n  width: 100%;\n  height: 100%;\n  overflow: auto;\n}\n\n* {\n  font-family: 'Roboto';\n}\n\n.right-section-container {\n  width: 100%;\n  height: 100%;\n}\n\n.appnameediticon {\n  cursor: pointer;\n  width: 24px;\n  height: 24px;\n\n  font-family: 'Material Design Icons';\n  font-style: normal;\n  font-weight: 400;\n  font-size: 24px;\n  line-height: 24px;\n  text-align: center;\n  color: #151233;\n  flex: none;\n  order: 0;\n  flex-grow: 0;\n  position: absolute;\n  margin-left: 410px;\n  top: 30px;\n}\n\n.selected-lottie {\n  background-color: transparent;\n  border-radius: 4px;\n  border: 1px solid #BCBCBC;\n  color: #212121;\n  padding-left: 12px;\n}\n\n.select-lottie {\n  top: 22px;\n  position: absolute;\n  width: 400px;\n  height: 40px;\n  font-size: 15px;\n  flex: none;\n  order: 1;\n  align-self: stretch;\n  flex-grow: 0;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  width: 400px;\n  height: 40px;\n  box-sizing: border-box;\n  padding: 8px 8px 8px 12px;\n}\n\n.informationicon:hover+.info-tooltip {\n  visibility: visible;\n  transition: opacity 0.3s ease-out 0.5s;\n  opacity: 1;