___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-type {\n  background-color: transparent;\n  color: #212121;\n}\n.custom-select .items {\n  color: #212121;\n  border-radius: 8px;\n  border: 1px solid #E3E3E3;\n  overflow: hidden;\n  background-color: #FFFFFF;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n.custom-select .items .option {\n  font-size: 14px;\n  padding-left: 12px;\n}\n.custom-select .items .option:hover {\n  background-color: #EEEEEE;\n  color: #212121;\n}\n.select-realm-source-type i,\n.select-subdomain-source-type i {\n  font-size: 16px;\n  color: #BCBCBC;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/oAuthSettings.vue"],"names":[],"mappings":";AAwlBA;EACE,6BAA6B;EAC7B,cAAc;AAChB;AAEA;EACE,cAAc;EACd,kBAAkB;EAClB,yBAAyB;EACzB,gBAAgB;EAChB,yBAAyB;EACzB,0CAA0C;AAC5C;AAEA;EACE,eAAe;EACf,kBAAkB;AACpB;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB;AAEA;;EAEE,eAAe;EACf,cAAc;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, onMounted, ref } from \"vue\";\nimport { IAppSettings } from \"../../../../../../common/qCloudTemp/application\";\nimport { ModelContants } from \"../../../../../../common/qCloudTemp/modelContants\";\nimport { UsageType } from \"../../../../../../common/qCloudTemp/quickCloud\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IModel, IModule } from \"../../../../domain/model/models\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport CheckboxComp from \"../../components/checkboxComp.vue\";\nimport IDEInput from \"../application/IDEInput.vue\";\nimport CustomSelect from \"../customSelect.vue\";\n\n\n\ninterface IOAuthSettingsProp {\n  oAuthSettings: IAppSettings['oAuthSettings']\n}\ninterface IOAuthPageData {\n  id: string;\n  parentId?: string;\n  collapsed?: boolean;\n  anyChildChecked?: boolean;\n  allChildChecked?: boolean;\n  checked?: boolean;\n  name: string;\n  type: IModel[\"modelType\"] | IModule[\"objectType\"];\n  // version?: string;\n  // disabled?: boolean | true;\n  children?: Array<IOAuthPageData>;\n  usageType?: UsageType;\n  // imported?: boolean;\n}\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst emit = defineEmits([\"getSettingsModel\"]);\nconst props = defineProps<IOAuthSettingsProp>();\nconst items = ref<Array<IOAuthPageData>>([]);\nconst isActive = ref(props.oAuthSettings!.active || false);\nconst oAuthIdentityURL = ref(props.oAuthSettings!.idendityProviderURL || '');\nconst oAuthClientID = ref(props.oAuthSettings!.clientId || '');\nconst oAuthAdvancedSettings = ref(true);\nconst oAuthUnsecurePages = ref(false);\nconst realmSourceType = ref<'Constant' | 'From Query String' | undefined>(props.oAuthSettings!.isRealmFromQS == true ? 'From Query String' : props.oAuthSettings!.isRealmFromQS == false ? 'Constant' : undefined);\nconst subdomainSourceType = ref<'constant' | 'query' | 'currentDomain' | undefined>(props.oAuthSettings!.subdomainSource ?? undefined);\nconst realmConstantValue = ref(props.oAuthSettings!.realm);\nconst subdomainValue = ref(props.oAuthSettings!.subdomainValue ?? \"myDomain\");\nconst identityProviderURL = computed(() => oAuthIdentityURL.value.length > 0 ? oAuthIdentityURL.value : 'identityProviderURL')\nconst realmSourceUrl = computed(() => realmSourceType.value == 'Constant' ? '&realm=' + realmConstantValue.value : realmSourceType.value == 'From Query String' ? '&realm={yourRealm}' : '');\nconst languageType = ref<'Constant' | 'From Query String' | undefined>(props.oAuthSettings!.isLangFromQS == true ? 'From Query String' : props.oAuthSettings!.isLangFromQS == false ? 'Constant' : undefined);\nconst language = ref(props.oAuthSettings!.language ?? \"TR-tr\");\n// const localeQueryStringKeyUrl = computed(() => 'http://<yoursiteURL>?' + (localeSourceType.value == 'From Query String' ? localeQueryStringKey.value : 'locale') + '=<language>');\nconst languageUrl = computed(() => languageType.value == 'Constant' ? '&lang=' + language.value : '&lang={yourSiteLanguage}');\nconst subdomainUrl = computed(() => subdomainSourceType.value == 'constant' ? '&subdomain=' + subdomainValue.value : subdomainSourceType.value == \"query\" ? '&subdomain={yourSubDomain}' : '')\nconst redirectUriShowUrl = computed(() => redirectUri.value.length > 0 ? '&redirectUri=' + redirectUri.value : '&redirectUri={yoursiteUrl}')\nconst iamURL = computed(() => identityProviderURL.value + '?' + realmSourceUrl.value + subdomainUrl.value + redirectUriShowUrl.value + languageUrl.value);\nconst redirectUri = ref(props.oAuthSettings!.redirectUri || '');\nconst tokenEndPoint = ref(props.oAuthSettings!.tokenEndPoint || '');\nconst clientSecret = ref(props.oAuthSettings!.clientSecret || '');\nconst preserveTokens = ref(props.oAuthSettings!.preserveTokens || false);\nconst unsecurePages = ref<Array<string>>([]);\n\nonMounted(() => {\n  handleOAuthPageItems();\n})\n\nfunction setActive(value: boolean) {\n  isActive.value = value;\n  emit(\"getSettingsModel\", { 'active': isActive.value })\n}\n\nfunction setIdentityURL(url: string) {\n  oAuthIdentityURL.value = url;\n  emit(\"getSettingsModel\", { 'idendityProviderURL': oAuthIdentityURL.value })\n}\nfunction setClientID(id: string) {\n  oAuthClientID.value = id;\n  emit(\"getSettingsModel\", { 'clientId': oAuthClientID.value })\n\n}\nfunction selectLanguageSource(type: 'Select' | 'Constant' | 'From Query String') {\n  switch (type) {\n    case \"Constant\":\n    case \"From Query String\":\n      languageType.value = type;\n      break;\n    case 'Select': languageType.value = undefined; break;\n  }\n  emit(\"getSettingsModel\", { 'isLangFromQS': languageType.value == \"From Query String\" ? true : languageType.value == \"Constant\" ? false : undefined })\n}\nfunction setLanguage(key: string) {\n  if (key && key.length > 0 && languageType.value == 'Constant') { language.value = key; }\n  else { language.value = \"\"; }\n  emit(\"getSettingsModel\", { 'language': language.value })\n}\nfunction setPreserveTokens(value: boolean) {\n  preserveTokens.value = !preserveTokens.value;\n  emit(\"getSettingsModel\", { 'preserveTokens': preserveTokens.value })\n}\n\nfunction selectRealmSource(type: 'Select' | 'Constant' | 'From Query String') {\n  switch (type) {\n    case 'Constant': case 'From Query String': realmSourceType.value = type; break;\n    case 'Select': realmSourceType.value = undefined; break;\n  }\n  emit(\"getSettingsModel\", { 'isRealmFromQS': realmSourceType.value == \"From Query String\" ? true : realmSourceType.value == \"Constant\" ? false : undefined })\n}\nfunction setRealmConstantValue(value: string) {\n  if (value && value.length > 0 && realmSourceType.value == \"Constant\") { realmConstantValue.value = value; }\n  else { realmConstantValue.value = \"\"; }\n  emit(\"getSettingsModel\", { 'realm': realmConstantValue.value })\n}\nfunction selectSubdomainSource(type: 'Select' | 'Constant' | 'From Query String' | 'From Current Subdomain') {\n  switch (type) {\n    case 'Constant': subdomainSourceType.value = 'constant'; break;\n    case 'From Query String': subdomainSourceType.value = 'query'; break;\n    case 'From Current Subdomain': subdomainSourceType.value = 'currentDomain'; break;\n    case 'Select': subdomainSourceType.value = undefined; break;\n  }\n  emit(\"getSettingsModel\", { 'subdomainSource': subdomainSourceType.value })\n}\nfunction setSubdomainUrl(url: string) {\n  if (url && url.length > 0 && subdomainSourceType.value == 'constant') { subdomainValue.value = url; }\n  else { subdomainValue.value = \"myDomain\"; }\n  emit(\"getSettingsModel\", { 'subdomainValue': subdomainValue.value })\n}\nfunction getRedirectUri(uri: string) {\n  redirectUri.value = uri\n  emit(\"getSettingsModel\", { 'redirectUri': redirectUri.value })\n}\nfunction getTokenEndPoint(uri: string) {\n  tokenEndPoint.value = uri\n  emit(\"getSettingsModel\", { 'tokenEndPoint': tokenEndPoint.value })\n}\nfunction getClientSecret(uri: string) {\n  clientSecret.value = uri\n  emit(\"getSettingsModel\", { 'clientSecret': clientSecret.value })\n}\nfunction handleOAuthPageItems() {\n  items.value = viewModel.studio.items.filter(item => item.objectType == \"module\" && item.children.length > 0).map(item => {\n    const oAuthPageData: IOAuthPageData = {\n      id: item.ID,\n      collapsed: true,\n      name: item.name,\n      type: \"module\",\n      children: (item as IModule).children.filter((child) => child.objectType == \"model\").map(child => child as IModel).map(child => {\n        const childModel: IOAuthPageData = { id: child.ID, parentId: item.ID, name: child.name, type: (child as IModel).modelType }\n        return { ...childModel, checked: props.oAuthSettings?.unsecurePages?.includes(item.name) } as IOAuthPageData\n      })\n    }\n    handleParentCheckedStatus(oAuthPageData);\n    return oAuthPageData;\n  })\n  viewModel.studio.items\n    .filter((item) => item.objectType == \"model\" && item.modelType != \"appSettings\" && item.usageType != \"appSettings\" && item.usageType != \"appAssetList\" && item.modelType == \"qjson\")\n    .map(item => item as IModel)\n    .forEach(item => {\n      items.value.push({\n        id: item.ID,\n        name: item.name,\n        checked: props.oAuthSettings?.unsecurePages?.includes(item.name),\n        type: item.modelType,\n      });\n    });\n}\nfunction handleParentCheckedStatus(item: IOAuthPageData) {\n  item.allChildChecked = item.children && item.children.length > 0 && !item.children.some((child) => !child.checked);\n  item.anyChildChecked = item.children?.some((child) => child.checked);\n}\nfunction handleCollapse(item: IOAuthPageData) {\n  item.collapsed = !item.collapsed;\n}\nfunction handleItemCheckEvent(item: IOAuthPageData) {\n  if (item.type != \"module\") {\n    item.checked = !item.checked;\n    item.parentId && handleParentCheckedStatus(items.value.find((i) => i.id == item.parentId)!)\n  } else {\n    if (item.children && item.children.length > 0) {\n      if (item.anyChildChecked) {\n        item.children.forEach(child => child.checked = false);\n        item.checked = false;\n      } else {\n        item.children.forEach(child => child.checked = true);\n        item.checked = true;\n      }\n      handleParentCheckedStatus(item);\n    }\n    else {\n      item.checked = !item.checked;\n      item.allChildChecked = false;\n    }\n  }\n  handleUnsecurePages();\n  // unsecurePages.value.push(item.name);\n  // emit(\"getSettingsModel\", {'unsecurePages': unsecurePages.value})\n}\nfunction handleUnsecurePages() {\n  unsecurePages.value = [];\n  for (const item of items.value) {\n    if (item.children) {\n      for (const child of item.children) {\n        if (child.checked) {\n          unsecurePages.value.push(child.name);\n        }\n      }\n    }\n    else {\n      if (item.checked) {\n        unsecurePages.value.push(item.name);\n      }\n    }\n  }\n  emit(\"getSettingsModel\", { 'unsecurePages': unsecurePages.value })\n}\n\n</script>\n<template>\n  <div>\n    <div class=\"oAuth-desc\">\n      <span>oAuth Configuration screen lets you easily connect your app with Identity and Access Management (IAM) systems.\n        To set up oAuth for your app, you need to fill in the relevant fields.</span>\n    </div>\n    <div class=\"oAuth-doc-activate\">\n      <div class=\"oAuth-activate\">\n        <input class=\"oAuth-switch round\" type=\"checkbox\" v-model=\"isActive\"\n          @click=\"isActive = !isActive, setActive(isActive)\" />\n        <span>Activate</span>\n      </div>\n      <div class=\"oAuth-doc-info\">\n        <i class=\"mdi mdi-information\"></i>\n        <span>Use <a class=\"oAuth-doc-link\" href=\"https://docs.onplateau.com/UI-Screens/UI-Settings/oAuth-management\"\n            target=\"_blank\">oAuth Documentation</a> for\n          detailed information. </span>\n      </div>\n    </div>\n    <div class=\"main-inputs-div\">\n      <div style=\"width:50%\">\n        <IDEInput elementId=\"identityURLInput\" @getValue=\"setIdentityURL\" placeholder=\"Identity Provider URL\" type=\"text\" :value=\"oAuthIdentityURL\"\n          info-text=\"Identity Provider URL\" />\n      </div>\n      <div style=\"width:50%; margin-left: 28px\">\n        <IDEInput elementId=\"clientIDInput\" @getValue=\"setClientID\" placeholder=\"Client ID\" type=\"text\" info-text=\"Client ID\"\n          :value=\"oAuthClientID\" />\n      </div>\n    </div>\n    <div>\n      <div class=\"protocol-settings-unsecure-pages-tabs\">\n        <div @click=\"oAuthAdvancedSettings = true; oAuthUnsecurePages = false\"\n          :class=\"oAuthAdvancedSettings ? 'active-category' : ''\">Advanced Protocol Settings</div>\n        <div @click=\"oAuthUnsecurePages = true; oAuthAdvancedSettings = false\"\n          :class=\"oAuthUnsecurePages ? 'active-category' : ''\">Unsecure Pages</div>\n      </div>\n      <div v-if=\"oAuthAdvancedSettings\">\n        <div class=\"advanced-settings\">\n          <div style=\"width: 50%\">\n            <div class=\"source-settings\">\n              <div class=\"source-name\">\n                <span>Realm Source</span>\n              </div>\n              <div class=\"sources\">\n                <CustomSelect :options=\"['Select', 'Constant', 'From Query String']\" class=\"select-realm-source-type\"\n                  :default=\"realmSourceType\" :placeholder=\"'Select'\" @input=\"selectRealmSource\"></CustomSelect>\n              </div>\n            </div>\n            <div class=\"source-settings\">\n              <div class=\"source-name\">\n                <span>Language</span>\n              </div>\n              <div class=\"sources\">\n                <CustomSelect :options=\"['Select', 'Constant', 'From Query String']\" class=\"select-realm-source-type\"\n                  :default=\"languageType\" :placeholder=\"'Select'\" @input=\"selectLanguageSource\"></CustomSelect>\n              </div>\n            </div>\n            <div class=\"source-settings\">\n              <div class=\"source-name\">\n                <span>Subdomain Source</span>\n              </div>\n              <div class=\"sources\">\n                <CustomSelect :options=\"['Select', 'Constant', 'From Query String', 'From Current Subdomain']\"\n                  class=\"select-subdomain-source-type\" :placeholder=\"'Select'\" @input=\"selectSubdomainSource\"\n                  :default=\"subdomainSourceType == 'constant' ? 'Constant' : subdomainSourceType == 'query' ? 'From Query String' : subdomainSourceType == 'currentDomain' ? 'From Current Subdomain' : undefined\">\n                </CustomSelect>\n              </div>\n            </div>\n            <div class=\"source-settings\">\n              <IDEInput elementId=\"redirectUriInput\" @getValue=\"getRedirectUri\" info-text=\"Redirect Uri\" placeholder=\"Redirect Uri\" type=\"text\"\n                :value=\"redirectUri\" />\n            </div>\n          </div>\n          <div style=\"width: 50%\">\n            <div style=\"margin-bottom: 24px\">\n              <IDEInput :isDisabled=\"realmSourceType != 'Constant'\" elementId=\"realmConstantInput\" @getValue=\"setRealmConstantValue\" placeholder=\"Realm Constant Value\"\n                type=\"text\"  info-text=\"Realm Constant Value\" :value=\"realmConstantValue\" />\n            </div>\n            <div style=\"margin-bottom: 24px\">\n              <IDEInput :isDisabled=\"languageType != 'Constant'\" elementId=\"languageConstantInput\" @getValue=\"setLanguage\" placeholder=\"Language Constant Value\" type=\"text\"\n                info-text=\"Language Constant Value\" :value=\"language\" />\n            </div>\n            <div>\n              <IDEInput :isDisabled=\"subdomainSourceType !== 'constant'\" elementId=\"subdomainUrlInput\" @getValue=\"setSubdomainUrl\"\n                placeholder=\"Subdomain Url Value\" type=\"text\" info-text=\"Subdomain Url Value\" :value=\"subdomainValue\" />\n            </div>\n          </div>\n        </div>\n        <div class=\"oAuth-iam-url\" style=\"font-size: 15px; width: 100%; margin-bottom: 10px; padding: 12px;\">\n          <span><span style=\"font-weight: 700\">oAuth Url:</span> {{ iamURL }} </span>\n        </div>\n        <div\n          v-if=\"realmSourceType == 'From Query String' || subdomainSourceType == 'query' || languageType == 'From Query String'\"\n          class=\"oAuth-query-string-info\" style=\"font-size: 15px; width: 100%;\">\n          <i class=\"mdi mdi-information\"></i>\n          <span>To use oAuth with query strings, add them to your URL. See <a class=\"oAuth-doc-link\"\n              href=\"https://docs.onplateau.com/UI-Screens/UI-Settings/oAuth-management\" target=\"_blank\">oAuth\n              Documentation</a> for more details.</span>\n        </div>\n        <div style=\"width: 50%\">\n          <div class=\"source-settings\" style=\" margin-top: 24px;\">\n            <div class=\"source-name\">\n              <span>Preserve Tokens</span>\n            </div>\n            <div class=\"sources\">\n              <div class=\"oAuth-activate\">\n                <input class=\"oAuth-switch round\" type=\"checkbox\" v-model=\"preserveTokens\"\n                  @click=\"setPreserveTokens(preserveTokens)\" />\n                <span>Active</span>\n              </div>\n            </div>\n          </div>\n          <div class=\"source-settings\">\n            <IDEInput @getValue=\"getTokenEndPoint\" info-text=\"Token EndPoint\" placeholder=\"Token EndPoint\" type=\"text\"\n              :value=\"tokenEndPoint\" />\n          </div>\n          <div class=\"source-settings\">\n            <IDEInput @getValue=\"getClientSecret\" info-text=\"Client Secret\" placeholder=\"Client Secret\" type=\"text\"\n              :value=\"clientSecret\" />\n          </div>\n        </div>\n      </div>\n      <div v-if=\"oAuthUnsecurePages\" class=\"unsecure-pages\">\n        <div v-for=\"item in items\" :key=\"item.id\">\n          <div v-if=\"item.type == 'module'\">\n            <div class=\"row-container\">\n              <i :class=\"'mdi mdi-menu' + (item.collapsed ? '-right' : '-down') + ' chevron' + (!item.children || item.children.length == 0 ? ' disabled' : '')\"\n                @click=\"handleCollapse(item)\"></i>\n              <CheckboxComp class=\"module-checkbox\" :check=\"item.allChildChecked\"\n                :minus=\"!item.allChildChecked && item.anyChildChecked\" @click=\"handleItemCheckEvent(item)\" />\n              <span>{{ item.name }}</span>\n            </div>\n\n            <div v-show=\"!item.collapsed\" v-for=\"child in item.children\" :key=\"child.id\" class=\"row-container\">\n              <CheckboxComp class=\"model-checkbox\" :check=\"child.checked\" @click=\"handleItemCheckEvent(child)\" />\n              <i v-if=\"child.type != 'module'\" :class=\"ModelContants.getModelVisualInfo(child.type, undefined).icon\"\n                :style=\"{ color: ModelContants.getModelVisualInfo(child.type, undefined).color }\"></i>\n              <span class=\"model-name\">{{ child.name }}</span>\n            </div>\n          </div>\n          <div v-else class=\"row-container\" style=\"margin-left: 16px;\">\n            <CheckboxComp class=\"module-checkbox\" :check=\"item.checked\" @click=\"handleItemCheckEvent(item)\" />\n            <i :class=\"ModelContants.getModelVisualInfo(item.type, undefined).icon\"\n              :style=\"{ color: ModelContants.getModelVisualInfo(item.type, undefined).color }\"></i>\n            <span class=\"model-name\">{{ item.name }}</span>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.oAuth-desc {\n  font-size: 16px;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: left;\n  color: #616161;\n}\n\n.oAuth-doc-activate {\n  display: flex;\n  align-items: center;\n  margin-top: 35px;\n}\n\n.oAuth-activate {\n  display: flex;\n  align-items: center;\n}\n\n.oAuth-activate span {\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n}\n\n.oAuth-switch {\n  position: relative;\n  cursor: pointer;\n  top: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n  margin-right: 13px;\n  margin-left: auto;\n}\n\n.oAuth-switch:after {\n  position: absolute;\n  content: \"\";\n  height: 14px;\n  width: 14px;\n  background-color: white;\n  -webkit-transition: 0.4s;\n  transition: 0.4s;\n  border-radius: 50%;\n}\n\n.oAuth-switch:disabled:after {\n  background-color: #757575;\n}\n\n.oAuth-switch:checked {\n  background-color: #151233;\n}\n\ninput[type=checkbox] {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  -webkit-tap-highlight-color: transparent;\n  cursor: pointer;\n}\n\n.oAuth-switch:checked:after {\n  transform: translatex(16px);\n}\n\n.oAuth-switch.round::after {\n  left: 3px;\n  border-radius: 50%;\n}\n\n.oAuth-switch.round {\n  width: 36px;\n  height: 20px;\n  border-radius: 34px;\n  padding: 3px;\n}\n\n.oAuth-doc-info,\n.oAuth-iam-url,\n.oAuth-query-string-info {\n  padding: 5px 12px;\n  color: #757575;\n  font-size: 14px;\n  background: #F5F5FA;\n  border-radius: 4px;\n  display: flex;\n  align-items: center;\n  margin-left: auto;\n  width: 456px;\n}\n\n.oAuth-doc-info i,\n.oAuth-iam-url i,\n.oAuth-query-string-info i {\n  font-size: 24px;\n  color: #151233;\n  margin-right: 8px;\n}\n\n.oAuth-doc-link {\n  color: #151233\n}\n\n.main-inputs-div {\n  display: flex;\n  align-items: center;\n  margin-top: 24px;\n  margin-bottom: 24px;\n}\n\n.protocol-settings-unsecure-pages-tabs {\n  display: flex;\n  align-items: center;\n  width: 100%\n}\n\n.protocol-settings-unsecure-pages-tabs div {\n  color: #A1A1A1;\n  font-weight: 500;\n  font-size: 14px;\n  width: 282px;\n  padding-bottom: 8px;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  cursor: pointer;\n  border: 2px solid transparent;\n}\n\n.active-category {\n  color: #151233 !important;\n  border-bottom: 2px solid !important\n}\n\n.advanced-settings {\n  display: flex;\n  padding: 24px 0 0;\n}\n\n.unsecure-pages {\n  padding: 24px 0;\n}\n\n.source-settings .radio-button-name,\n.source-settings .sources {\n  display: flex;\n  align-items: center;\n  margin-bottom: 15px;\n}\n\n.source-settings {\n  margin-bottom: 24px;\n  margin-right: 28px;\n}\n\n.source-settings .source-name {\n  margin-bottom: 3px;\n}\n\n.source-settings .source-name span {\n  font-size: 15px;\n  font-weight: 500;\n  line-height: 18px;\n}\n\n.source-settings .source-name i {\n  color: #757575;\n  font-size: 20px;\n  line-height: 20px;\n  margin-left: 8px;\n}\n\n.source-type {\n  font-size: 14px;\n  line-height: 16px;\n  letter-spacing: 0em;\n  color: #212121;\n\n}\n\n.source-settings .source-name i:hover+.info-tooltip {\n  visibility: visible;\n  transition: opacity 0.3s ease-out 0.5s;\n  opacity: 1;\n}\n\n.info-tooltip {\n  visibility: hidden;\n  position: absolute;\n  background: #757575;\n  color: #FFFFFF;\n  font-size: 11px !important;\n  text-align: center;\n  border-radius: 4px;\n  letter-spacing: 0.2px;\n  padding: 6px 8px 6px 8px;\n  opacity: 0;\n  max-width: 350px;\n}\n\n.row-container {\n  display: flex;\n}\n\n.module-checkbox {\n  margin: 3px 25px 0 5px\n}\n\n.model-checkbox {\n  margin: 3px 25px 0 5px;\n  margin-left: 40px;\n}\n\n.model-name {\n  color: #212121;\n  margin-left: 5px;\n}\n\n.select-realm-source-type,\n.select-subdomain-source-type {\n  width: 100%;\n  font-size: 15px;\n  height: 40px;\n  line-height: 40px;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  padding: 0px 12px;\n}\n</style>\n<style>\n.selected-type {\n  background-color: transparent;\n  color: #212121;\n}\n\n.custom-select .items {\n  color: #212121;\n  border-radius: 8px;\n  border: 1px solid #E3E3E3;\n  overflow: hidden;\n  background-color: #FFFFFF;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n\n.custom-select .items .option {\n  font-size: 14px;\n  padding-left: 12px;\n}\n\n.custom-select .items .option:hover {\n  background-color: #EEEEEE;\n  color: #212121;\n}\n\n.select-realm-source-type i,\n.select-subdomain-source-type i {\n  font-size: 16px;\n  color: #BCBCBC;\n}\n</style>"],"sourceRoot":""}]);
